import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Button from '@splunk/react-ui/Button';
import Heading from '@splunk/react-ui/Heading';
import Star from '@splunk/react-icons/Star';
import Tiles from '@splunk/react-icons/Tiles';
import { _ } from '@splunk/ui-utils/i18n';
import { createTestHook } from '../../util/TestSupport';
import { ENTITY_VIEW_TAB } from '../../util/StateConstants';

export default class GroupsEmptyStatePage extends Component {
  static propTypes = {
    /* Toggles to the entities view when clicked */
    onViewEntitiesClick: PropTypes.func.isRequired,
  };

  constructor(props, context) {
    super(props, context);
    this.cssStyles = {
      layout: {
        textAlign: 'center',
        marginTop: '50px',
      },
      tile: {
        marginTop: '5px',
        marginRight: '5px',
        color: 'grey',
      },
      heading1: {
        textAlign: 'center',
      },
      heading3: {
        marginTop: '15px',
        marginBottom: '30px',
        textAlign: 'center',
        marginRight: '35%',
        marginLeft: '35%',
      },
      paragraph: {
        textAligh: 'center',
      },
    };
  }

  handleButtonClick = () => {
    this.props.onViewEntitiesClick(ENTITY_VIEW_TAB);
  }

  render() {
    const heading1 = _("You haven't created a group yet.");
    const headingContent = 'To create a group, use dimensions in the filter bar on the entity lister page \n' +
      'and use the star to save the filtered set as a group.  You can also click \n' +
      'on any dimensions in the list of entities to apply that dimension and value to \n' +
      'the filter bar';
    const heading3 = _(headingContent);
    return (
      <div
        style={this.cssStyles.layout}
        {...createTestHook(__filename, name)}
      >
        <div>
          <Star
            style={this.cssStyles.tile}
            width="96px"
            height="96px"
            preserveAspectRatio="xMaxYMid"
          />
        </div>
        <Heading
          level={1}
          style={this.cssStyles.heading1}
        >
          {heading1}
        </Heading>
        <Heading
          level={3}
          style={this.cssStyles.heading3}
        >
          {heading3}
        </Heading>
        <Button
          appearance="primary"
          label="View All Entities"
          id="view-entity-btn"
          icon={<Tiles />}
          onClick={this.handleButtonClick}
        />
      </div>
    );
  }
}



