import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { cloneDeep, last, without } from 'lodash';
import Multiselect from '@splunk/react-ui/Multiselect';
import { _ } from '@splunk/ui-utils/i18n';
import { createTestHook } from '../../util/TestSupport';
import { UNHEALTHY_FLAT } from './Colors';

/**
 * Multiselect component specifically for dealing with wildcards
 *
 * @export
 * @class WildcardMultiSelect
 * @extends {Component}
 */
export default class WildcardMultiSelect extends Component {
  static propTypes = {
    /**
     * Array of string options
     */
    options: PropTypes.arrayOf(PropTypes.string).isRequired,


    /**
     * The list of values that have already been selected
     */
    selectedValues: PropTypes.arrayOf(PropTypes.string),

    /**
     * Callback for whenever filter text is entered
     *   Arguments:
     *     filterString - keyword filter to be searched for
     *   Returns: none. The parent is responsible for updating options
     */
    onFilterChange: PropTypes.func.isRequired,

    /**
     * Callback for whenever a value is selected
     *   Arguments: Array of values
     *   Returns: none
     */
    onValueChange: PropTypes.func.isRequired,

    /**
     * Value to show in box when empty
     */
    placeholder: PropTypes.string,

    /**
     * When true, the dropdown will display a loading signal of some sort
     */
    isLoadingOptions: PropTypes.bool,

    /**
     * Styling for the underlying Multiselect component
     */
    multiselectStyle: PropTypes.object,

    /**
     * Show loading animation
     * @type {boolean}
     */
    animateLoading: PropTypes.bool,
  }

  static defaultProps = {
    selectedValues: [],
    placeholder: _('Start typing'),
    isLoadingOptions: false,
    multiselectStyle: {},
    animateLoading: true,
  }

  /**
   * Creates an instance of WildcardMultiSelect.
   *
   * @memberOf WildcardMultiSelect
   */
  constructor(props, ...rest) {
    super(props, ...rest);

    this.errorStates = {
      onlyOneWildcard: _('Only one wildcard character allowed.'),
      wildcardOnlyAtEnd: _('Wildcard can only be at the end.'),
    };

    this.state = {
      errorState: null,
    };

    this.filterString = '';
  }

  /**
   * Generates options for populate the multiselect dropdown
   *
   * @memberOf WildcardMultiSelect
   */
  generateOptions = () => {
    const options = [];

    // All selected values need to be an option that's hidden.
    // During filtering or errors, we don't end up generating an option
    // for an already selected value, which causes it to show up weirdly.
    // In that case, we need to generate the hidden ones separately.
    const optionsToHide = cloneDeep(this.props.selectedValues);
    const optionsToShow = this.state.errorState ? [] :
      without(this.props.options, optionsToHide);

    // Add the wildcard option
    if (!this.state.errorState && last(this.filterString) === _('*')) {
      options.push(<Multiselect.Option
        label={this.filterString}
        value={this.filterString}
        key={this.filterString}
      />);
    }

    options.push(...optionsToShow.map(option =>
      (<Multiselect.Option
        label={option}
        value={option}
        key={option}
        hidden
      />)));

    options.push(...optionsToHide.map(option =>
      (<Multiselect.Option
        label={option}
        value={option}
        key={option}
        hidden
      />)));

    return options;
  }

  /**
   * Handler for when a value is selected in the multiselect. This calls
   * back onValueChange to allow the parent component to update its
   * internal state.
   *
   * @memberOf WildcardMultiSelect
   */
  handleValueChange = (e, { values }) => {
    this.props.onValueChange(values);
    this.props.onFilterChange('');
    this.filterString = null;
  }

  /**
   * Handler for any text typed into multiselect. This calls back
   * onFilterChange with the expectation that the parent component
   * updates the options
   *
   * @memberOf WildcardMultiSelect
   */
  handleFilterChange = (e, { keyword }) => {
    let errorState = null;
    let callbackFilter = keyword;

    if (keyword.includes(_('*'))) {
      if (keyword.match(/[*]/g).length > 1) {
        // Keyword has more than one wildcard character
        errorState = 'onlyOneWildcard';
      } else if (last(keyword) !== _('*')) {
        // Keyword has characters following the wildcard character
        errorState = 'wildcardOnlyAtEnd';
      } else {
        callbackFilter = keyword.slice(0, -1);
      }
    }

    this.setState({
      errorState,
    });

    if (!errorState) {
      this.props.onFilterChange(callbackFilter);
      this.filterString = keyword;
    }
  }

  /**
   * Generates the options to display and renders
   *
   * @memberOf WildcardMultiSelect
   */
  render() {
    const options = this.generateOptions();
    const errorMessage = this.state.errorState ? this.errorStates[this.state.errorState] : '';

    return (
      <Multiselect
        {...createTestHook(__filename, name)}
        style={this.props.multiselectStyle}
        values={this.props.selectedValues}
        placeholder={this.props.placeholder}
        onChange={this.handleValueChange}
        onFilterChange={this.handleFilterChange}
        animateLoading={this.props.animateLoading}
        isLoadingOptions={this.props.isLoadingOptions}
        error={!!this.state.errorState}
        noOptionsMessage={!!this.state.errorState &&
          <span style={{ color: UNHEALTHY_FLAT }}>{errorMessage}</span>
        }
        inline
        controlledFilter
      >
        {options}
      </Multiselect>
    );
  }
}



