import React from 'react';
import PropTypes from 'prop-types';

/**
 * A non-interactive menu item used to visually separate groups of items in the menu.
 */
function Divider(props) {
  const { addLeftMargin, addRightMargin, ...otherProps } = props;

  return (
    <div
      style={{
        backgroundColor: 'rgba(194,197,204,0.5)',
        width: '1px',
        height: '20px',
        verticalAlign: 'middle',
        marginLeft: addLeftMargin ? '15px' : '0px',
        marginRight: addRightMargin ? '15px' : '0px',
        display: 'inline-block',
      }}
      {...otherProps}
    />
  );
}

Divider.propTypes = {
  addLeftMargin: PropTypes.bool,
  addRightMargin: PropTypes.bool,
};

Divider.defaultProps = {
  addLeftMargin: false,
  addRightMargin: false,
};

export default Divider;



