import { cloneDeep, isNil } from 'lodash';
import { dimensionObjectsToList } from './Dimensions';
import {
  ENTITY_HEALTH,
} from '../components/investigate/tileviews/EntityTileConstants';

export const addDimensionToEntities = entities => entities.map((entity) => {
  const modifiedEntity = cloneDeep(entity);

  modifiedEntity.dimensions = dimensionObjectsToList(
    modifiedEntity.identifierDimensions,
    modifiedEntity.informationalDimensions,
  );

  return modifiedEntity;
});


export const computeEntityHealth = (entityName,
  thresholdDataValue, entityThresholdValue, entityThresholdEvaluator) => {
  // const thresholdDataValue = this.props.entityThresholdData[entityName];
  if (isNil(thresholdDataValue)) {
    return ENTITY_HEALTH.UNKNOWN;
  }

  return entityThresholdEvaluator(
    thresholdDataValue,
    entityThresholdValue,
  )
    ? ENTITY_HEALTH.UNHEALTHY
    : ENTITY_HEALTH.HEALTHY;
};



