import {
  GROUP_DELETE,
  START,
  SUCCESS,
  FAILURE,
} from '../actionTypes';
import ActionUtils from '../actionUtils';

export const GROUP_DELETION_TRACKER = ActionUtils.createRequestTypes(GROUP_DELETE);

export const deleteGroupTracker = {
  start: () => ActionUtils.createAction(GROUP_DELETION_TRACKER[START], {}),
  success: response => ActionUtils.createAction(GROUP_DELETION_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(GROUP_DELETION_TRACKER[FAILURE], { error }),
};

export const deleteGroups = (groupNames, deleteAll) => ActionUtils.createAction(
  GROUP_DELETE, groupNames, deleteAll);



