/**
Entities listing page
Uses the SplunkUI table component and search manager to populate information.
Limitations:
 - Does one large call to the entity API to fetch all entities
 - All entity and dimension filtering is done client-side
 */

import React from 'react';
import PropTypes from 'prop-types';
import { difference, includes } from 'lodash';

import Table from '@splunk/react-ui/Table';
import Link from '@splunk/react-ui/Link';
import Paginator from '@splunk/react-ui/Paginator';
import Message from '@splunk/react-ui/Message';
import P from '@splunk/react-ui/Paragraph';
import Menu from '@splunk/react-ui/Menu';
import Dropdown from '@splunk/react-ui/Dropdown';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import { _ } from '@splunk/ui-utils/i18n';
import Switch from '@splunk/react-ui/Switch';
import Select from '@splunk/react-ui/Select';
import Button from '@splunk/react-ui/Button';

import { createTestHook } from '../../util/TestSupport';
import TagPill from '../common/TagPill';
import ObjectStatus from '../common/ObjectStatus';
import ConfirmationDialog from '../common/ConfirmationDialog';
import { DELETE_OBJECT_STATE, ADMIN_ROLE } from '../../util/StateConstants';
import { getAppPageUrl } from '../../libs/EMUtils';
import {
  isAllSelected,
  isSelected,
  updateSelectedItems,
} from '../../util/Investigate';

export default class EntitiesListPageContents extends React.Component {
  /**
   * Generates a link to the Metrics Analysis Workspace
   *
   * @static
   * @param {any} entityRow the entity row??
   * @returns a string that represents the URL
   *
   * @memberOf EntitiesListPageContents
   */
  static getEntityUrl(entityRow) {
    // Link to specific entity MAW.
    const encodedEntityName = encodeURIComponent(entityRow.name);
    let filters = { host: [null] };
    if (entityRow.identifierDimensions) {
      filters = entityRow.identifierDimensions;
    }
    const encodedFilter = encodeURIComponent(JSON.stringify(filters));
    return getAppPageUrl(`metrics_analysis?entity=${encodedEntityName}&filters=${encodedFilter}`);
  }

  /**
   * Formats epoch time to a human-readable string
   *
   * @static
   * @param {any} unixEpochTimeDate the date in epoch notation
   * @returns a readable time string
   *
   * @memberOf EntitiesListPageContents
   */
  static formatUnixDate(unixEpochTimeDate) {
    const formatDate = new Date(0);
    formatDate.setUTCSeconds(unixEpochTimeDate);
    return formatDate.toString();
  }

  static propTypes = {
    /**
     * List of entities to be displayed
     */
    filteredEntities: PropTypes.array,
    /**
     * The key to sort the entities by: hostname, status, lastCollected
     */
    entitySortKey: PropTypes.oneOf(['title', 'state', 'updated_date']),
    /**
     * The order of sorting: asc, desc
     */
    entitySortBy: PropTypes.oneOf(['asc', 'desc']),
    /**
     * Callback for when an entity is to be sorted
     */
    onEntitySort: PropTypes.func.isRequired,
    /**
     * Callback for when a dimension is selected or deleted
     */
    onDimensionSelectionChange: PropTypes.func.isRequired,
    /**
     * Number of entities to display per page
     */
    entitiesPerPage: PropTypes.oneOf([-1, 20, 50, 100]),
    /**
     * The entity count offset
     */
    entitiesOffset: PropTypes.number,
    /**
     * Callback for entity deletion
     */
    onDeleteEntities: PropTypes.func.isRequired,
    /**
     * Callback for function delete that confirms the deletion
     */
    onEntityDeletionConfirm: PropTypes.func.isRequired,
    /**
     * Callback for function that finishes or abandons confirmation
     */
    onEntityDeletionReady: PropTypes.func.isRequired,
    /**
     * Selected entities to delete
     */
    selectedEntities: PropTypes.array,
    /**
     * String indicating the state of entity deletion
     */
    entityDeletionState: PropTypes.oneOf(Object.values(DELETE_OBJECT_STATE)),
    /**
     * Callback for when an entity is selected or deselected
     */
    onEntitySelection: PropTypes.func.isRequired,
    /**
     * total entity count
     */
    totalEntityCount: PropTypes.number,
    /**
     * roles for the currently authenticated user
     */
    userRoles: PropTypes.array,
    /**
     * Entity last fetched timestamp
     */
    entitiesLastFetched: PropTypes.number,
    /**
     * Callback for when the pagination occurs, sets selectedEntities to []
     */
    onClearSelectedEntities: PropTypes.func.isRequired,
    /**
     * Callback for when pagination occurs
     */
    onEntityOffsetChange: PropTypes.func.isRequired,
    /**
     * Callback for loading metric names
     */
    loadMetricNames: PropTypes.func.isRequired,
    /**
     * Callback for loading dimensions
     */
    loadDimensions: PropTypes.func.isRequired,
    /**
     * Callback for loading entities
     */
    loadEntities: PropTypes.func.isRequired,
    /**
     * Callback for loading roles for current authenticated user
     */
    loadRoles: PropTypes.func.isRequired,
    /**
     * Callback for when entity fetch limit changes
     */
    onEntitiesPerPageChange: PropTypes.func.isRequired,
    /**
     * Possible values for number of entities to display per page
     */
    entitiesPerPageOptions: PropTypes.arrayOf(PropTypes.number),
  };

  static defaultProps = {
    filteredEntities: [],
    entitySortKey: 'title',
    entitySortBy: 'asc',
    earliestTime: '-7d@d',
    onTimeRangeChange: () => {},
    entitiesPerPage: 50,
    entitiesOffset: 0,
    entityDeletionState: DELETE_OBJECT_STATE.READY,
    selectedEntities: [],
    totalEntityCount: 0,
    userRoles: [],
    entitiesLastFetched: -1,
    entitiesPerPageOptions: [20, 50, 100],
  };

  constructor(props) {
    super(props);
    this.cssStyles = {
      main: {
        margin: '0px 20px 0px 20px',
      },
      tagPill: {
        marginRight: '5px',
        marginTop: '5px',
        display: 'inline-block',
      },
      tagPillContainer: {
        display: 'inline-block',
      },
      deleteWaitSpinner: {
        margin: '4px 4px',
      },
      paginatorContainer: {
        height: '40px',
        lineHeight: '40px',
        padding: '5px 0px 5px 5px',
        display: 'flex',
        justifyContent: 'space-between',
      },
      actionsCount: {
        display: 'flex',
      },
      bulkActions: {
        display: 'inline-block',
      },
      entitiesSelectedCount: {
        marginLeft: '20px',
        marginTop: '11px',
      },
      selectAllLink: {
        marginLeft: '10px',
        marginTop: '1px',
        fontSize: '14px',
      },
    };

    // sortKey needs to match the key in entity object
    this.tableSortableColumns = [
      { sortKey: 'title', label: _('Entity Name') },
      { sortKey: 'state', label: _('Status') },
      { sortKey: 'updated_date', label: _('Last time data collected') },
    ];

    this.state = {
      selectAll: false,
    };

    this.handleSelectAllInPage = this.handleSelectAllInPage.bind(this);
    this.handleSingleSelect = this.handleSingleSelect.bind(this);
  }

  componentDidMount() {
    this.props.loadEntities();
    this.props.loadDimensions();
    this.props.loadMetricNames();
    this.props.loadRoles();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.entityDeletionState === DELETE_OBJECT_STATE.SUCCESS) {
      this.props.onEntityDeletionReady();
    }
  }

  getConfirmationDialog() {
    let title = '';
    let message = '';
    let mainMessage = '';
    let infoMessage = '';
    const cannotDelete = !includes(this.props.userRoles, ADMIN_ROLE);
    const confirmDialogContents = this.generateAppropriateConfirmationContents();
    switch (this.props.entityDeletionState) {
      case DELETE_OBJECT_STATE.CONFIRM: {
        title = confirmDialogContents.title;
        mainMessage = confirmDialogContents.confirmationMessage;
        infoMessage = confirmDialogContents.infoMessage;
        message = (
          <div>
            <P>{mainMessage}</P>
            {
              (infoMessage && <Message type="info">{infoMessage}</Message>)
            }
          </div>
        );
        break;
      }
      case DELETE_OBJECT_STATE.ERROR: {
        title = _('Deletion Failed');
        mainMessage = _('Deletion failed!');
        message = <P>{mainMessage}</P>;
        break;
      }
      case DELETE_OBJECT_STATE.IN_PROGRESS: {
        title = _('Deleting...');
        message = <WaitSpinner style={this.cssStyles.deleteWaitSpinner} />;
        break;
      }
      default:
        break;
    }
    return (
      <ConfirmationDialog
        open={
          this.props.entityDeletionState === DELETE_OBJECT_STATE.CONFIRM ||
          this.props.entityDeletionState === DELETE_OBJECT_STATE.IN_PROGRESS ||
          this.props.entityDeletionState === DELETE_OBJECT_STATE.ERROR
        }
        title={title}
        primaryButtonText={_('Delete')}
        className="entity-deletion-confirmation-dialog"
        primaryButtonClicked={() => this.handleEntitiesDelete()}
        beforeClose={() => this.beforeClose()}
        showCancel
        closeOnClick={false}
        disablePrimaryClick={this.props.entityDeletionState === DELETE_OBJECT_STATE.IN_PROGRESS ||
          cannotDelete}
      >
        {message}
      </ConfirmationDialog>
    );
  }

  getEntitiesPerPageDropDown() {
    return (
      <Select
        appearance="pill"
        value={`${this.props.entitiesPerPage}`}
        onChange={this.handlePerPageEntitiesChange}
        id="entities-per-page"
      >
        {this.props.entitiesPerPageOptions.map(option =>
          <Select.Option key={`${option}`} label={`${option} Per Page`} value={`${option}`} />,
        )}
      </Select>
    );
  }

  getSelectAllEntitiesLink() {
    const boolAllEntitiesInPageSelected = isAllSelected(
      this.props.filteredEntities,
      this.props.selectedEntities,
      'name',
    );
    const totalPages = Math.ceil(
      this.props.totalEntityCount / this.props.entitiesPerPage,
    );
    const selectAllText =
      boolAllEntitiesInPageSelected &&
      !this.state.selectAll &&
      totalPages !== 1 &&
      _(`Select All ${this.props.totalEntityCount} Entities`);
    const clearAllText =
      boolAllEntitiesInPageSelected &&
      this.state.selectAll &&
      totalPages !== 1 &&
      _('Clear Selection');

    return (
      boolAllEntitiesInPageSelected && (
        <Link
          id="select-all-items"
          onClick={() => this.handleSelectAll()}
          style={this.cssStyles.selectAllLink}
        >
          {selectAllText}
          {clearAllText}
        </Link>
      )
    );
  }

  generateEntitiesSelectedCount() {
    let selectionCountText;
    if (this.props.selectedEntities.length === 1) {
      selectionCountText = _(
        `${this.props.selectedEntities.length} Entity Selected`,
      );
    } else if (this.state.selectAll) {
      selectionCountText = _(
        `${this.props.totalEntityCount} Entities Selected`,
      );
    } else {
      selectionCountText = _(
        `${this.props.selectedEntities.length} Entities Selected`,
      );
    }

    return (
      this.props.selectedEntities.length > 0 && (
        <P
          id="entities-selected-count"
          style={this.cssStyles.entitiesSelectedCount}
        >
          {selectionCountText}
        </P>
      )
    );
  }

  generateAppropriateConfirmationContents() {
    const isDeleteSingleEntity = this.props.selectedEntities.length === 1;
    const isDeleteMultipleEntities =
      this.props.selectedEntities.length > 1 &&
      this.props.selectedEntities.length <= this.props.filteredEntities.length;
    const isDeleteAllEntities = this.state.selectAll;
    const isAdmin = includes(this.props.userRoles, ADMIN_ROLE);
    let title;
    let confirmationMessage;
    let infoMessage;

    if (!isAdmin) {
      const entityVerbage = (isDeleteSingleEntity ? _('the selected entity!') : _('the selected entities!'));
      title = _(`Cannot delete ${entityVerbage}`);
      confirmationMessage = _(`Only admin can delete ${entityVerbage}`);
    } else if (isDeleteAllEntities) {
      title = _('Delete All Entities');
      confirmationMessage = _(
        `Are you sure you want to delete all ${this.props
          .totalEntityCount} entities in your environment?`,
      );
      infoMessage = _(
        'Deleting all entities will only remove them from the list. \n' +
        'All associated alerts will also be deleted.',
      );
    } else if (isDeleteMultipleEntities) {
      title = _(`Delete ${this.props.selectedEntities.length} Entities`);
      confirmationMessage = _(
        `Are you sure you want to delete the ${this.props.selectedEntities
          .length} selected entities?`,
      );
      infoMessage = _(
        'Deleting these entities will only remove them from the list. \n' +
        'All associated alerts will also be deleted.',
      );
    } else if (isDeleteSingleEntity) {
      title = _('Delete Entity');
      confirmationMessage = _(
        `Are you sure you want to delete the entity ${this.props
          .selectedEntities[0].title}?`,
      );
      infoMessage = _(
        'Deleting this entity will only remove it from the list. \n' +
        'All associated alerts will also be deleted.',
      );
    }

    return {
      title,
      confirmationMessage,
      infoMessage,
    };
  }

  beforeClose() {
    if (this.state.selectAll) {
      this.setState({ selectAll: false });
    }
    this.props.onEntityDeletionReady();
  }

  handleConfirmBulkDelete() {
    this.props.onEntityDeletionConfirm(this.props.selectedEntities);
  }

  handleConfirmDelete(row) {
    this.props.onEntityDeletionConfirm(row);
  }

  /**
   * Handles when the dimension value in the table is clicked
   *
   * @memberOf EntitiesListPageContents
   */
  handleDimensionTableClick = (e, { value }) => {
    this.props.onDimensionSelectionChange(value, false);
  };

  handleEntitiesDelete() {
    this.props.onDeleteEntities({
      entityNames: this.props.selectedEntities.map(entity => entity.name),
      deleteAll: this.state.selectAll,
    });
  }

  /**
   * Handles page selection for the listing table
   *
   * @memberOf EntitiesListPageContents
   */
  handlePageChange = (e, { page }) => {
    this.props.onEntityOffsetChange((page - 1) * this.props.entitiesPerPage);
  };

  handlePerPageEntitiesChange = (e, { value }) => {
    this.props.onEntitiesPerPageChange(parseInt(value, 10));
  };

  /**
   * Handle select all entities
   */
  handleSelectAll() {
    if (this.state.selectAll) {
      this.props.onClearSelectedEntities();
    }
    this.setState({ selectAll: !this.state.selectAll });
  }

  /**
   * Handle select all entities in page
   */
  handleSelectAllInPage() {
    const visibleEntities = this.props.filteredEntities;
    const entitiesToBeToggled = difference(
      visibleEntities,
      this.props.selectedEntities,
    );
    if (entitiesToBeToggled.length > 0) {
      this.props.onEntitySelection(
        updateSelectedItems(
          this.props.selectedEntities,
          entitiesToBeToggled,
          'name',
        ),
      );
    } else {
      this.props.onEntitySelection(
        updateSelectedItems(
          this.props.selectedEntities,
          visibleEntities,
          'name',
        ),
      );
    }
  }

  /**
   * Handle selecting a single entity
   */
  handleSingleSelect = (e, { value }) => {
    this.props.onEntitySelection(
      updateSelectedItems(this.props.selectedEntities, [value], 'name'),
    );
  };

  /**
   * Handles sorting of the entity listing table
   *
   * @memberOf EntitiesListPageContents
   */
  handleSort = (e, { sortKey }) => {
    this.props.onEntitySort(sortKey);
  };

  generateTable() {
    const deleteLabel = _('Delete');
    const entityAction = (
      <Button
        label={_('Action')}
        appearance="pill"
        isMenu
      />
    );
    return (
      <Table stripeRows>
        <Table.Head>
          <Table.HeadCell width={60}>
            <Switch
              key="checkall"
              value="checkall"
              onClick={this.handleSelectAllInPage}
              selected={isAllSelected(
                this.props.filteredEntities,
                this.props.selectedEntities,
                'name',
              )}
            />
          </Table.HeadCell>
          {this.tableSortableColumns.map(columnHeaderData => (
            <Table.HeadCell
              key={columnHeaderData.sortKey}
              onSort={this.handleSort}
              sortKey={columnHeaderData.sortKey}
              sortDir={
                columnHeaderData.sortKey === this.props.entitySortKey
                  ? this.props.entitySortBy
                  : 'none'
              }
            >
              {columnHeaderData.label}
            </Table.HeadCell>
          ))}
          <Table.HeadCell>{_('Dimensions')}</Table.HeadCell>
          <Table.HeadCell>{_('Actions')}</Table.HeadCell>
        </Table.Head>
        {
          <Table.Body>
            {this.props.filteredEntities.map(entityRow => (
              <Table.Row key={entityRow.name}>
                <Table.Cell>
                  <Switch
                    key={entityRow.name}
                    value={entityRow}
                    onClick={this.handleSingleSelect}
                    selected={isSelected(
                      this.props.selectedEntities,
                      entityRow.name,
                      'name',
                    )}
                  />
                </Table.Cell>
                <Table.Cell>
                  <Link to={EntitiesListPageContents.getEntityUrl(entityRow)}>
                    {entityRow.title}
                  </Link>
                </Table.Cell>
                <Table.Cell>
                  <ObjectStatus status={entityRow.state} />
                </Table.Cell>
                <Table.Cell style={{ width: '300px' }}>
                  {EntitiesListPageContents.formatUnixDate(
                    entityRow.updatedDate,
                  )}
                </Table.Cell>
                <Table.Cell style={{ paddingTop: 0 }}>
                  <div style={this.cssStyles.tagPillContainer}>
                    {entityRow.dimensions.map(dimension => (
                      <TagPill
                        key={`${dimension[0]}:${dimension[1]}`}
                        style={this.cssStyles.tagPill}
                        data={{ type: dimension[0], name: dimension[1] }}
                        value={{ type: dimension[0], name: dimension[1] }}
                        onClick={this.handleDimensionTableClick}
                      />
                    ))}
                  </div>
                </Table.Cell>
                <Table.Cell style={{ paddingLeft: '0px' }}>
                  <Dropdown toggle={entityAction} style={{ minWidth: '60px' }}>
                    <Menu>
                      <Menu.Item
                        onClick={() => this.handleConfirmDelete(entityRow)}
                      >
                        {deleteLabel}
                      </Menu.Item>
                    </Menu>
                  </Dropdown>
                </Table.Cell>
              </Table.Row>
            ))}
          </Table.Body>
        }
      </Table>
    );
  }

  render() {
    const totalPages = Math.ceil(
      this.props.totalEntityCount / this.props.entitiesPerPage,
    );
    const currentPage = (this.props.entitiesOffset / this.props.entitiesPerPage) + 1;
    const confirmationDialog = this.getConfirmationDialog();
    const deleteSelectedEntities = _('Delete Selected Entities');
    const table = this.generateTable();
    const numberOfEntitiesPage = this.getEntitiesPerPageDropDown();
    const bulkActions = (
      <Button
        label={_('Bulk Actions')}
        id="bulk-actions-btn"
        disabled={this.props.selectedEntities.length === 0}
        appearance="pill"
        isMenu
      />
    );
    return (
      <div
        style={this.cssStyles.main}
        data-updated={this.props.entitiesLastFetched}
        {...createTestHook(__filename, name)}
      >
        {confirmationDialog}
        <div>
          <div style={this.cssStyles.paginatorContainer}>
            <div style={this.cssStyles.actionsCount}>
              <div style={this.cssStyles.bulkActions}>
                <Dropdown toggle={bulkActions} id="bulk-actions">
                  <Menu>
                    <Menu.Item onClick={() => this.handleConfirmBulkDelete()}>
                      {deleteSelectedEntities}
                    </Menu.Item>
                  </Menu>
                </Dropdown>
              </div>
              {this.generateEntitiesSelectedCount()}
              {this.getSelectAllEntitiesLink()}
            </div>
            <div>
              {numberOfEntitiesPage}
              <Paginator
                onChange={this.handlePageChange}
                current={currentPage}
                totalPages={totalPages}
                alwaysShowLastPageLink
              />
            </div>
          </div>
          {table}
        </div>
      </div>
    );
  }
}



