import React from 'react';
import PropTypes from 'prop-types';
import Success from '@splunk/react-icons/Success';
import Warning from '@splunk/react-icons/Warning';
import NotAllowed from '@splunk/react-icons/NotAllowed';
import { _ } from '@splunk/ui-utils/i18n';
import { HEALTHY_FLAT, UNHEALTHY_FLAT, UNKNOWN_FLAT } from './Colors';

/**
 * Simple class to display color and test for status. Can be
 * used with lister pages
 *
 * @export
 * @class ObjectStatus
 * @extends {React.Component}
 */
export default class ObjectStatus extends React.Component {
  static propTypes = {
    status: PropTypes.oneOf(['active', 'disabled', 'inactive']).isRequired,
    showDisplayText: PropTypes.bool,
  }

  static defaultProps = {
    showDisplayText: true,
  }

  static stateInfo = {
    active: { color: HEALTHY_FLAT, displayText: _('Active'), icon: Success },
    disabled: { color: UNKNOWN_FLAT, displayText: _('Disabled'), icon: NotAllowed },
    inactive: { color: UNHEALTHY_FLAT, displayText: _('Inactive'), icon: Warning },
  }

  static cssStyles = {
    main: {
      position: 'relative',
      marginRight: '5px',
    },
    span: {
      display: 'inline-block',
      marginLeft: '5px',
      lineHeight: '17px',
      position: 'absolute',
      bottom: '0',
    },
  }

  render() {
    const { color, displayText, icon } = ObjectStatus.stateInfo[this.props.status];
    const StatusIcon = icon;

    return (
      <div style={ObjectStatus.cssStyles.main}>
        <StatusIcon width="12" height="12" style={{ color }} />
        {this.props.showDisplayText &&
        <span style={ObjectStatus.cssStyles.span}>
          {`${displayText}`}
        </span>
        }
      </div>
    );
  }
}



