import { createRESTURL } from '@splunk/splunk-utils/url';

import { requestAPI } from './util';


export function restartSplunk() {
  return requestAPI(createRESTURL('server/control/restart'), '', 'POST', {});
}

export function loadUserRoles() {
  return requestAPI(
    createRESTURL('authentication/current-context'), '', 'GET', {});
}



