import React from 'react';
import PropTypes from 'prop-types';
import Button from '@splunk/react-ui/Button';
import Plus from '@splunk/react-icons/Plus';
import Minus from '@splunk/react-icons/Minus';

import { createTestHook } from '../../../util/TestSupport';
import { ICON_ENABLED, ICON_DISABLED } from '../../common/Colors';

const buttonValues = {
  IN: 'in',
  OUT: 'out',
};

export default class TileZoom extends React.Component {
  static propTypes = {
    /**
     * Callback when '+' is clicked
     */
    onZoomIn: PropTypes.func.isRequired,
    /**
     * Callback when '-' is clicked
     */
    onZoomOut: PropTypes.func.isRequired,
    /**
     * Disable the '+'
     */
    disableZoomIn: PropTypes.bool,
    /**
     * Disable the '-'
     */
    disableZoomOut: PropTypes.bool,
  };

  static defaultProps = {
    disableZoomIn: false,
    disableZoomOut: false,
  };

  constructor(props) {
    super(props);

    this.styles = {
      default: {
        display: 'block',
        width: '30px',
        height: '30px',
        borderRadius: '0px',
        backgroundColor: 'white',
        borderColor: '#eee',
      },
      zoomOut: {
        marginLeft: '0px',
        borderTopColor: 'white',
      },
      iconStyleEnabled: {
        fontSize: '18px',
        color: ICON_ENABLED,
      },
      iconStyleDisabled: {
        fontSize: '18px',
        color: ICON_DISABLED,
      },
    };
  }

  handleClick = (e, { value }) => {
    if (value === buttonValues.IN) {
      this.props.onZoomIn();
    } else if (buttonValues.OUT) {
      this.props.onZoomOut();
    }
  }

  render() {
    return (
      <div {...createTestHook(__filename, name)}>
        <Button
          style={this.styles.default}
          appearance="pill"
          inline
          disabled={this.props.disableZoomIn}
          onClick={this.handleClick}
          value={buttonValues.IN}
        >
          <Plus
            style={
              this.props.disableZoomIn
                ? this.styles.iconStyleDisabled
                : this.styles.iconStyleEnabled
            }
          />
        </Button>
        <Button
          style={{ ...this.styles.default, ...this.styles.zoomOut }}
          appearance="pill"
          inline
          disabled={this.props.disableZoomOut}
          onClick={this.handleClick}
          value={buttonValues.OUT}
        >
          <Minus
            style={
              this.props.disableZoomOut
                ? this.styles.iconStyleDisabled
                : this.styles.iconStyleEnabled
            }
          />
        </Button>
      </div>
    );
  }
}



