import React from 'react';
import PropTypes from 'prop-types';

import { createTestHook } from '../../../util/TestSupport';
import TileZoom from './TileZoom';

/* eslint react/prefer-stateless-function: 'off' */
export default class TileLayout extends React.Component {
  static propTypes = {
    /**
     * Has to be a single child of type CardLayout
     */
    children: PropTypes.element.isRequired,

    /**
     * className prop to make it easy to identify the type
     */
    className: PropTypes.string.isRequired,

    /**
     * Timestamp for when data was last fetched
     */
    dataLastFetched: PropTypes.number.isRequired,

    /**
     * An ordered list of supported resolutions
     * Zoomed out -> zoomed in
     */
    supportedResolutions: PropTypes.array.isRequired,

    /**
     * Callback when tile resolution changes
     */
    onTileResolutionChange: PropTypes.func.isRequired,

    /**
     * The tile resolution to display
     */
    tileResolution: PropTypes.string.isRequired,
  };

  constructor(props) {
    super(props);

    this.styles = {
      zoomControl: {
        position: 'fixed',
        right: 0,
        bottom: 0,
        margin: '15px',
      },
    };
  }

  render() {
    const {
      supportedResolutions,
      tileResolution,
      onTileResolutionChange,
    } = this.props;
    const tileResolutionIndex = supportedResolutions.indexOf(tileResolution);
    const tileZoomControl = (this.props.className !== 'group-tile' ? (
      <TileZoom
        disableZoomIn={
          tileResolutionIndex === supportedResolutions.length - 1
        }
        disableZoomOut={tileResolutionIndex === 0}
        onZoomIn={() =>
          onTileResolutionChange(
            supportedResolutions[tileResolutionIndex + 1],
          )}
        onZoomOut={() =>
          onTileResolutionChange(
            supportedResolutions[tileResolutionIndex - 1],
          )}
      />
    ) : '');

    return (
      <div
        {...createTestHook(__filename, name)}
        className={this.props.className}
        style={{ display: 'flex' }}
        data-updated={this.props.dataLastFetched}
      >
        {this.props.children}
        <div style={this.styles.zoomControl}>
          {tileZoomControl}
        </div>
      </div>
    );
  }
}



