import React from 'react';
import PropTypes from 'prop-types';
import Radium from 'radium';
import { abbreviateNumber } from '@splunk/ui-utils/format';
import { _ } from '@splunk/ui-utils/i18n';
import Clock from '@splunk/react-icons/Clock';
import Error from '@splunk/react-icons/Error';
import { createTestHook } from '../../../util/TestSupport';
import EntitiesListPageContents from '../EntitiesListing';
import { ENTITY_HEALTH, TILE_STYLES_MEDIUM } from './EntityTileConstants';
import { AVAILABILITY } from '../ColorByOption';
import {
  truncateTitle,
  formatMultipleValues,
  extractDimensionValue,
} from '../../../util/Investigate';
import { getTimeDifference } from '../../../util/Common';

const EntityTileMedium = class EntityTileMedium extends React.Component {
  static propTypes = {
    /** The entity to be displayed in the tile */
    entity: PropTypes.object,
    /** If true, displays the threshold data value */
    entityThresholdMetric: PropTypes.string,
  };

  static defaultProps = {
    entity: {},
    entityThresholdMetric: AVAILABILITY,
  };

  constructor(props) {
    super(props);

    this.styles = { ...TILE_STYLES_MEDIUM };
  }

  getCardHoverState(key) {
    return Radium.getState(this.state, key, ':hover');
  }

  render() {
    const internalKey = `${this.props.entity.name}`;

    return (
      <div
        {...createTestHook(__filename, name)}
        role="button"
        tabIndex={0}
        key={internalKey}
        style={{
          ...this.styles.cardBody,
          ...this.styles.entityHealthAttributes[this.props.entity.entityHealth],
        }}
        onClick={() => {
          window.location.href = EntitiesListPageContents.getEntityUrl(this.props.entity);
        }}
        data-test-health={this.props.entity.entityHealth}
      >
        {this.props.entity.entityHealth === ENTITY_HEALTH.UNHEALTHY && (
          <Error style={this.styles.errorIcon} />
        )}
        <div
          className="title-container"
          style={this.styles.title}
          data-test-name={this.props.entity.title}
        >
          {truncateTitle(this.props.entity.title)}
        </div>
        {(this.props.entity.entityHealth === ENTITY_HEALTH.UNHEALTHY ||
          this.getCardHoverState(internalKey)) && (
            <div>
              {this.props.entityThresholdMetric !== AVAILABILITY &&
              <span className="metric-value" style={this.styles.lastUpdated}>
                {abbreviateNumber(this.props.entity.thresholdDataValue)}
              </span>
              }
              {this.props.entityThresholdMetric === AVAILABILITY &&
                this.props.entity.entityHealth === ENTITY_HEALTH.UNHEALTHY &&
                <span className="lastUpdateTime" style={this.styles.lastUpdated}>
                  <Clock style={this.styles.clock} />
                  {_(`${getTimeDifference(this.props.entity.updatedDate)}`)}
                </span>
              }
              <span className="assetInfo" style={this.styles.assetInfo}>
                {this.props.entity.dimensionDisplayNames
                  .slice(0, 2)
                  .map(dimension => (
                    <div key={`${internalKey}-${dimension.dimension_name}`}>
                      {formatMultipleValues(
                        extractDimensionValue(
                          this.props.entity.dimensions,
                          dimension.dimension_name,
                        ),
                      )}
                    </div>
                  ))}
              </span>
            </div>
          )
        }
      </div>
    );
  }
};

export default Radium(EntityTileMedium);



