import { takeLatest, put, select } from 'redux-saga/effects';
import { stringify } from 'query-string';
import { isArray, toInteger, toNumber, isString } from 'lodash';
import { selectEntityColorBy } from '../actions/investigate/ControlBar';
import updateRefreshInterval from '../actions/common/Refresh';
import { setEntityThresholdData, entityTileResolutionChange } from '../actions/investigate/EntitiesListing';
import { updateUrlParams, fetchWithOptions } from '../actions/common/url';

import {
  GROUP_VIEW_TAB,
  ENTITY_VIEW_TAB,
  TILE_VIEW,
  LIST_VIEW,
  TILE_RESOLUTION,
  INTERVAL_CHOICES,
  DEFAULT_INTERVAL,
} from '../util/StateConstants';

import * as actionTypes from '../actions/actionTypes';

const validateType = (typeString) => {
  if (typeString === GROUP_VIEW_TAB || typeString === ENTITY_VIEW_TAB) {
    return typeString;
  }
  return ENTITY_VIEW_TAB;
};

const validateViewType = (typeString) => {
  if (typeString === TILE_VIEW || typeString === LIST_VIEW) {
    return typeString;
  }
  return LIST_VIEW;
};

const validateRefreshInterval = (interval) => {
  const seconds = parseInt(interval, 10);
  if (INTERVAL_CHOICES.get(seconds)) {
    return seconds;
  }
  return DEFAULT_INTERVAL;
};

const validateTileSize = (tileSizeString) => {
  if (tileSizeString === TILE_RESOLUTION.SMALL || tileSizeString === TILE_RESOLUTION.MEDIUM) {
    return tileSizeString;
  }
  return TILE_RESOLUTION.MEDIUM;
};

const validateEntityDimensions = (dimensions, view) => {
  if (view === ENTITY_VIEW_TAB) {
    if (isArray(dimensions)) {
      return dimensions.map(str => str.split(','));
    }
    if (dimensions) {
      return [dimensions.split(',')];
    }
  }
  return undefined;
};

/* eslint-disable arrow-body-style */
const validateGroupFilter = (groups, view) => {
  if (view === GROUP_VIEW_TAB && groups) {
    return isArray(groups) ? groups : [groups];
  }
  return undefined;
};

const validatePage = (page, expectedPage, actualPage) => {
  if (expectedPage === actualPage) {
    return page || 1;
  }
  return undefined;
};

const getOffsetFromPageNumber = (page, fetchLimit) => {
  return (toInteger(page) - 1) * fetchLimit;
};

const getPageNumberFromOffset = (offset, itemsPerPage) => {
  return toInteger(offset / itemsPerPage) + 1;
};

const validateEntitySortValue = (sortValue, view) => {
  if (view === ENTITY_VIEW_TAB) {
    return isString(sortValue) ? sortValue : undefined;
  }
  return undefined;
};

const validateEntitySortBy = (sortBy, view) => {
  if (view === ENTITY_VIEW_TAB) {
    return sortBy === 'asc' || sortBy === 'desc' ? sortBy : undefined;
  }
  return undefined;
};

const validateGroupSortValue = (sortValue, view) => {
  if (view === GROUP_VIEW_TAB) {
    return isString(sortValue) ? sortValue : undefined;
  }
  return undefined;
};

const validateGroupSortBy = (sortBy, view) => {
  if (view === GROUP_VIEW_TAB) {
    return sortBy === 'asc' || sortBy === 'desc' ? sortBy : undefined;
  }
  return undefined;
};

const validateEntityThresholdMetric = (type, thresholdMetric) => {
  if (type === ENTITY_VIEW_TAB) {
    return thresholdMetric || undefined;
  }
  return undefined;
};

const validateEntityThresholdValue = (type, thresholdValue) => {
  if (type === ENTITY_VIEW_TAB) {
    return thresholdValue ? toNumber(thresholdValue) : undefined;
  }
  return undefined;
};

const urlStrigifyDimension = (dimensionArray) => {
  if (dimensionArray.length > 1) {
    return `${dimensionArray[0]},${dimensionArray[1]}`;
  }
  return '';
};

const validateUrlParams = (urlParams) => {
  const urlParamsClone = { ...urlParams };
  urlParamsClone.v = validateType(urlParamsClone.v);
  urlParamsClone.vt = validateViewType(urlParamsClone.vt);
  urlParamsClone.etr = validateTileSize(urlParamsClone.etr);
  urlParamsClone.ed = validateEntityDimensions(urlParamsClone.ed, urlParamsClone.v);
  urlParamsClone.gf = validateGroupFilter(urlParamsClone.gf, urlParamsClone.v);
  urlParamsClone.ep = validatePage(urlParamsClone.ep, ENTITY_VIEW_TAB, urlParamsClone.v);
  urlParamsClone.gp = validatePage(urlParamsClone.gp, GROUP_VIEW_TAB, urlParamsClone.v);
  urlParamsClone.esk = validateEntitySortValue(urlParamsClone.esk, urlParamsClone.v);
  urlParamsClone.esb = validateEntitySortBy(urlParamsClone.esb, urlParamsClone.v);
  urlParamsClone.etm = validateEntityThresholdMetric(urlParamsClone.v, urlParamsClone.etm);
  urlParamsClone.etv = validateEntityThresholdValue(urlParamsClone.v, urlParamsClone.etv);
  urlParamsClone.gsk = validateGroupSortValue(urlParamsClone.gsk, urlParamsClone.v);
  urlParamsClone.gsb = validateGroupSortBy(urlParamsClone.gsb, urlParamsClone.v);
  urlParamsClone.rfi = validateRefreshInterval(urlParamsClone.rfi);
  return urlParamsClone;
};

export const getControlBarReducer = state => state.controlBarReducer;
export const getEntitiesReducer = state => state.entitiesReducer;
export const getGroupsReducer = state => state.groupsReducer;
export const getDimensionMultiselectReducer = state => state.dimensionMultiselectReducer;

/**
* readAndParseUrl -- reads parameters from the url and yeilds the correct
* actions to set state. Then yields to set the url.
* @param object with attributes:
* v (view aka group or entity), vt (view type aka tile or list),
* ed (entity dimensions), gf (group filter), ep (entity page),
* gp (group page), esk (entity sort key), esb (entity sort by),
* gsk (group sort key), gsb (group sort by)
*/
export function* readAndParseUrl(urlData) {
  const validParams = validateUrlParams(urlData.data);
  const viewObj = {
    selectedTab: ENTITY_VIEW_TAB,
    selectedView: LIST_VIEW,
  };

  const entitiesReducer = yield select(getEntitiesReducer);
  const entitiesFetchLimit = entitiesReducer.entitiesFetchLimit;
  const groupsReducer = yield select(getGroupsReducer);
  const groupsFetchLimit = groupsReducer.groupsFetchLimit;

  const fetchOptionsObj = {
    sort_dir: 'asc',
    sort_key: 'title',
    count: validParams.v === GROUP_VIEW_TAB ? groupsFetchLimit : entitiesFetchLimit,
    offset: 0,
    selectedDimensions: [],
    groupFilters: [],
  };

  if (validParams.v === GROUP_VIEW_TAB) {
    viewObj.selectedTab = GROUP_VIEW_TAB;
  }
  if (validParams.vt === TILE_VIEW) {
    viewObj.selectedView = TILE_VIEW;
  }
  if (validParams.etr) {
    yield put(entityTileResolutionChange(validParams.etr));
  }
  if (validParams.gf) {
    fetchOptionsObj.groupFilters = validParams.gf;
  }
  if (validParams.gp) {
    fetchOptionsObj.offset = getOffsetFromPageNumber(validParams.gp, groupsFetchLimit);
  }
  if (validParams.ep) {
    fetchOptionsObj.offset = getOffsetFromPageNumber(validParams.ep, entitiesFetchLimit);
  }
  if (validParams.ed) {
    fetchOptionsObj.selectedDimensions = validParams.ed;
  }
  if (validParams.esb) {
    fetchOptionsObj.sort_dir = validParams.esb;
  }
  if (validParams.esk) {
    fetchOptionsObj.sort_key = validParams.esk;
  }
  if (validParams.etm) {
    if (entitiesReducer.threshold.metric !== validParams.etm) {
      yield put(selectEntityColorBy(validParams.etm));
    }
  }
  if (validParams.etv) {
    if (entitiesReducer.threshold.thresholdValue !== validParams.etv) {
      yield put(setEntityThresholdData(validParams.etv));
    }
  }
  if (validParams.gsk) {
    fetchOptionsObj.sort_key = validParams.gsk;
  }
  if (validParams.gsb) {
    fetchOptionsObj.sort_dir = validParams.gsb;
  }
  if (validParams.rfi) {
    yield put(updateRefreshInterval(validParams.rfi));
  }

  yield put(fetchWithOptions(fetchOptionsObj, viewObj));
  yield put(updateUrlParams(stringify(validParams)));
}

/**
* setUrl -- sets the url from interaction on the page
*/
export function* setUrl() {
  const controlBarReducer = yield select(getControlBarReducer);
  const entitiesReducer = yield select(getEntitiesReducer);
  const groupsReducer = yield select(getGroupsReducer);
  const dimensionMultiselectReducer = yield select(getDimensionMultiselectReducer);

  const view = controlBarReducer.currentTab;
  const viewType = controlBarReducer.selectedView;
  const groupFilter = groupsReducer.groupFilters;
  const entitiesOffset = entitiesReducer.entitiesOffset;
  const entityTileResolution = entitiesReducer.tileResolution;
  const groupsOffset = groupsReducer.groupsOffset;
  const entitySortKey = entitiesReducer.entitySortKey;
  let selectedDimensions;
  const entitySortBy = entitiesReducer.entitySortBy;
  const groupSortKey = groupsReducer.groupSortKey;
  const groupSortBy = groupsReducer.groupSortBy;
  const entitiesFetchLimit = entitiesReducer.entitiesFetchLimit;
  const groupsFetchLimit = groupsReducer.groupsFetchLimit;
  const entityThresholdValue = entitiesReducer.threshold.thresholdValue;
  const entityThresholdMetric = entitiesReducer.threshold.metric;
  const refreshInterval = controlBarReducer.refreshInterval;
  if (dimensionMultiselectReducer.selectedDimensions) {
    selectedDimensions = dimensionMultiselectReducer
      .selectedDimensions.map(urlStrigifyDimension);
  }

  const validParams = validateUrlParams({
    v: view,
    vt: viewType,
    etr: entityTileResolution,
    ed: selectedDimensions,
    gf: groupFilter,
    ep: getPageNumberFromOffset(entitiesOffset, entitiesFetchLimit),
    gp: getPageNumberFromOffset(groupsOffset, groupsFetchLimit),
    esk: entitySortKey,
    esb: entitySortBy,
    etv: entityThresholdValue,
    etm: entityThresholdMetric,
    gsk: groupSortKey,
    gsb: groupSortBy,
    rfi: refreshInterval,
  });

  yield put(updateUrlParams(stringify(validParams)));
}

export default function* urlSaga() {
  yield takeLatest(actionTypes.SET_STATE_FROM_URL, readAndParseUrl);
  yield takeLatest(actionTypes.ENTITY_TILE_RESOLUTION_CHANGE, setUrl);
  yield takeLatest(actionTypes.INVESTIGATE_TOGGLE, setUrl);
  yield takeLatest(actionTypes.TILE_LIST_TOGGLE, setUrl);
  yield takeLatest(actionTypes.ADD_DIMENSION_SELECTION, setUrl);
  yield takeLatest(actionTypes.UPDATE_SELECTED_DIMENSIONS, setUrl);
  yield takeLatest(actionTypes.UPDATE_GROUP_FILTERS, setUrl);
  yield takeLatest(actionTypes.ENTITY_OFFSET_CHANGE, setUrl);
  yield takeLatest(actionTypes.GROUP_OFFSET_CHANGE, setUrl);
  yield takeLatest(actionTypes.ENTITIES_LISTING_SORT_ENTITIES, setUrl);
  yield takeLatest(actionTypes.GROUPS_LISTING_SORT_GROUPS, setUrl);
  yield takeLatest(actionTypes.GROUPS_LISTING_SORT_BY, setUrl);
  yield takeLatest(actionTypes.ENTITY_THRESHOLD_INFO_CHANGE, setUrl);
  yield takeLatest(actionTypes.SELECT_ENTITY_COLOR_BY, setUrl);
  yield takeLatest(actionTypes.ENTITY_PER_PAGE_CHANGE, setUrl);
  yield takeLatest(actionTypes.GROUPS_PER_PAGE_CHANGE, setUrl);
  yield takeLatest(actionTypes.UPDATE_REFRESH_INTERVAL, setUrl);
}



