import React, { Component } from 'react';
import PropTypes from 'prop-types';

import P from '@splunk/react-ui/Paragraph';
import Select from '@splunk/react-ui/Select';
import SplunkwebConnector from '@splunk/react-time-range/SplunkwebConnector';
import TimeRangeDropdown from '@splunk/react-time-range/Dropdown';
import { _ } from '@splunk/ui-utils/i18n';
import { createGUID } from '@splunk/ui-utils/id';

import Star from '@splunk/react-icons/Star';
import Error from '@splunk/react-icons/Error';
import Success from '@splunk/react-icons/Success';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import {
  ICON_SELECTED,
  HEALTHY_FLAT,
  UNHEALTHY_FLAT,
} from '../common/Colors';

import ToggleButtonGroup from './ToggleButtonGroup';
import ConfirmationDialog from '../common/ConfirmationDialog';
import GroupContextControl from './GroupContextControl';
import ToggleTileListControl from './ToggleTileListControl';
import {
  FETCH_OBJECT_STATE,
  SAVE_OBJECT_STATE,
  ENTITY_VIEW_TAB,
  GROUP_VIEW_TAB,
  LIST_VIEW,
  TILE_VIEW,
} from '../../util/StateConstants';
import { AVAILABILITY } from './ColorByOption';
import ControlBar from '../common/ControlBar';
import Divider from '../common/Divider';
import ThresholdControl from '../common/ThresholdControl';
import DimensionMultiSelectContainer from '../../containers/common/DimensionMultiSelect';
import RefreshControl from '../common/RefreshControl';


export default class EntityControlBar extends Component {
  static propTypes = {
    /**
     * Flag to indicate whether the timerange picker should be shown
     */
    showTimeRangePicker: PropTypes.bool,

    /**
     * Callback when a new time range has been picked
     */
    onTimeRangeChange: PropTypes.func.isRequired,

    /**
     * The earliest time to examine
     */
    earliestTime: PropTypes.string,

    /**
     * The current group context
     */
    currentGroupContext: PropTypes.object,

    /**
     * Equivalent to GroupContextControl props
     */
    onCreateGroup: PropTypes.func.isRequired,
    onUpdateGroup: PropTypes.func.isRequired,

    /**
     * Equivalent to ToggleButtonGroup props
     */
    currentTab: PropTypes.string,
    onToggle: PropTypes.func.isRequired,

    /**
     * Function called on group save success dialog closed.
     * @type {function}
     */
    handleGroupSuccessDialogClose: PropTypes.func.isRequired,
    /**
     * A string representing the current state of group creation. The values
     * should be one of (READY,IN_PROGRESS, SUCCESS, ERROR)
     * @type {String}
     */
    groupSaveState: PropTypes.oneOf(Object.values(SAVE_OBJECT_STATE)),
    /**
     * Error message for group save failure.
     * @type {string}
     */
    createGroupPopoverMessage: PropTypes.string,
    /**
     * View mode for entities
     */
    selectedView: PropTypes.oneOf([LIST_VIEW, TILE_VIEW]),
    /**
     * Equivalent to ToggleTileListControl prop
     */
    onListTileToggle: PropTypes.func.isRequired,
    /**
     * The current selected color by option
     */
    entityThresholdMetric: PropTypes.string,
    /**
     * Function called on select color by option
     */
    onSelectEntityThresholdMetric: PropTypes.func.isRequired,
    /**
     * Entity refresh dispatch action
     * @type {[function]}
     */
    onRefreshEntities: PropTypes.func.isRequired,
    /**
     * total entity count
     */
    totalEntityCount: PropTypes.number,
    /**
     * total healthy entity count
     */
    healthyEntityCount: PropTypes.number,
    /**
     * total unhealthy entity count
     */
    unhealthyEntityCount: PropTypes.number,
    /**
     * State of the entity fetch
     */
    entityFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
    /**
     * State of the metric name fetch
     */
    metricNameFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
    /**
     * State of the metric data fetch
     */
    metricDataFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
    /**
     * List of metrics that we will use to color by
     */
    metricsList: PropTypes.array,
    /**
     * Callback for when a new threshold is selected
     */
    onUpdateEntityThresholdInfo: PropTypes.func.isRequired,
    entityThresholdValueMinimum: PropTypes.number.isRequired,
    entityThresholdValueMaximum: PropTypes.number.isRequired,
    entityThresholdValue: PropTypes.number.isRequired,
    entityThresholdEvaluator: PropTypes.func.isRequired,
    /**
     * The list of dimensions that have already been selected
     */
    selectedDimensions: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
    /**
     * Callback to compute entity health
     */
    onComputeEntitiesHealth: PropTypes.func.isRequired,
    /**
     * Auto-refresh interval
     */
    refreshInterval: PropTypes.number,
    /**
     * Callback to update refresh interval
     */
    onUpdateRefreshInterval: PropTypes.func.isRequired,
  }

  static defaultProps = {
    totalEntityCount: 0,
    healthyEntityCount: 0,
    unhealthyEntityCount: 0,
    entityFetchState: FETCH_OBJECT_STATE.READY,
    metricNameFetchState: FETCH_OBJECT_STATE.READY,
    metricDataFetchState: FETCH_OBJECT_STATE.READY,
    selectedDimensions: [],
    groupFilters: [],
    earliestTime: '-7d@d',
    showGroupModifyMenu: true,
    showTimeRangePicker: false,
    currentGroupContext: { name: '', title: '', filter: [] },
    currentTab: ENTITY_VIEW_TAB,
    groupSaveState: SAVE_OBJECT_STATE.READY,
    createGroupPopoverMessage: '',
    selectedView: LIST_VIEW,
    needsToRetrieveGroup: true,
    entityThresholdMetric: AVAILABILITY,
    metricsList: [],
    refreshInterval: -1,
  }

  constructor(props, context) {
    super(props, context);
    this.cssStyles = {
      timeRange: {
        display: 'inline-block',
      },
      multiselectGroup: {
        display: 'inherit',
        lineHeight: '40px',
        verticalAlign: 'middle',
        flexGrow: '100',
        marginLeft: '10px',
        marginRight: '20px',
      },
      multiselectStyle: {
        flexGrow: 'inherit',
        borderTopRightRadius: '0px',
        borderBottomRightRadius: '0px',
        borderRight: '0',
      },
      viewTypeSelector: {
        float: 'right',
        marginRight: '20px',
      },
      groupIconStyle: {
        backgroundColor: ICON_SELECTED,
        textAlign: 'center',
        width: '12px',
        height: '12px',
        padding: '2px',
        paddingBottom: '4px',
        margin: '0px 2px',
      },
      navContainer: {
        height: '100%',
        width: '100%',
        lineHeight: '40px',
        paddingLeft: '20px',
        display: 'flex',
        alignItems: 'center',
      },
      entityCountStyle: {
        marginRight: '8px',
        marginTop: '4px',
        display: 'flex',
      },
      refreshTextStyle: {
        fontSize: 24,
        fontWeight: 100,
      },
      refreshControlStyle: {
        fontSize: 24,
        fontWeight: 100,
        marginRight: '24px',
      },
      divider: {
        display: 'inline-block',
        marginTop: '1px',
      },
      colorByStyle: {
        marginTop: '8px',
        marginRight: '14px',
        display: 'flex',
      },
      countByHealthStyle: {
        marginTop: '6px',
        marginRight: '14px',
        display: 'flex',
      },
      errorIconStyle: {
        backgroundColor: UNHEALTHY_FLAT,
      },
      successIconStyle: {
        backgroundColor: HEALTHY_FLAT,
      },
      healthyEntity: {
        color: HEALTHY_FLAT,
        marginRight: '5px',
      },
      unhealthyEntity: {
        color: UNHEALTHY_FLAT,
        marginRight: '5px',
      },
    };

    this.onSaveGroup = this.onSaveGroup.bind(this);
    this.onUpdateGroup = this.onUpdateGroup.bind(this);
    this.constructGroupTitle = this.constructGroupTitle.bind(this);

    this.state = {
      currentGroupAction: null,
    };
  }

  /**
   * Saves the group details
   * @memberOf EntityControlBar
   */
  onSaveGroup(newGroupTitle) {
    this.props.onCreateGroup({
      name: createGUID(),
      title: newGroupTitle,
      filter: this.props.selectedDimensions,
    });
    this.setState({
      currentGroupAction: 'CREATE',
    });
  }

  /**
   * Update the group details
   * @memberOf EntityControlBar
   */
  onUpdateGroup(newGroupName) {
    this.props.onUpdateGroup({
      name: this.props.currentGroupContext.name,
      title: newGroupName,
      filter: this.props.selectedDimensions,
    });
    this.setState({
      currentGroupAction: 'UPDATE',
    });
  }

  onThresholdUpdate = (threshold) => {
    this.props.onUpdateEntityThresholdInfo(threshold);
    this.props.onComputeEntitiesHealth();
  }

  /**
  * Construct a confirmation dialog.
  * @return {string}
  */
  getConfirmationDialog() {
    let title = _('Group saved');
    let message =
      (<P>Group {this.props.currentGroupContext.title} is created. Switch to group view {
        <span style={this.cssStyles.groupIconStyle}>
          <Star width="12px" height="12px" preserveAspectRatio="xMaxYMid" style={{ color: 'white' }} />
        </span>
      } to view all your saved groups.</P>);

    switch (this.state.currentGroupAction) {
      case 'UPDATE':
        title = _('Group updated');
        message = (
          <P>Group {this.props.currentGroupContext.title} is updated. Switch to group view {
            <span style={this.cssStyles.groupIconStyle}>
              <Star width="12px" height="12px" preserveAspectRatio="xMaxYMid" style={{ color: 'white' }} />
            </span>
          } to view all your saved groups.</P>
        );
        break;
      default:
        break;
    }

    return (<ConfirmationDialog
      open={this.props.groupSaveState === SAVE_OBJECT_STATE.SUCCESS}
      title={title}
      primaryButtonText={_('View group now')}
      className="group-confirmation-dialog"
      beforeClose={this.props.handleGroupSuccessDialogClose}
      primaryButtonClicked={() => this.handleToggle(GROUP_VIEW_TAB)}
    >
      { message }
    </ConfirmationDialog>);
  }

  /**
  * Construct a suggested group title. Uses the values selected by the user.
  * @return {string}
  */
  constructGroupTitle() {
    const result = [];
    this.props.selectedDimensions.forEach((el) => {
      result.push(el[1]);
    });
    return result.join('-');
  }

  handleToggle = (selectedTab) => {
    if (selectedTab !== this.props.currentTab) {
      this.props.onToggle(selectedTab);
    }
  }

  handleColorByChange = (e, { value }) => {
    this.props.onSelectEntityThresholdMetric(value);
  }

  handleUpdateRefreshInterval = (interval) => {
    this.props.onUpdateRefreshInterval(interval);
  }

  render() {
    const multiselect =
      (
        <DimensionMultiSelectContainer
          multiselectStyle={this.cssStyles.multiselectStyle}
          placeholder={_('Start typing to filter through all dimensions')}
        />
      );

    const confirmationDialog = this.getConfirmationDialog();
    const createFilterString = filter => filter.map(dim => `${dim[0]}:${dim[1]}`).sort().join(',');
    const groupContextFilters = createFilterString(this.props.currentGroupContext.filter || []);
    const currentFilters = createFilterString(this.props.selectedDimensions || []);
    const entityCountString = this.props.totalEntityCount === 1 ? _(`${this.props.totalEntityCount} Entity`) :
      _(`${this.props.totalEntityCount} Entities`);
    const metricDropdownDisabled = (this.props.totalEntityCount === 0 ||
      this.props.metricNameFetchState === FETCH_OBJECT_STATE.IN_PROGRESS ||
      this.props.metricDataFetchState === FETCH_OBJECT_STATE.IN_PROGRESS
    );
    const healthyEntityCountString = this.props.entityThresholdMetric === AVAILABILITY ? _(`${this.props.healthyEntityCount} Available`) :
      _(`${this.props.healthyEntityCount} Healthy`);
    const unhealthyEntityCountString = this.props.entityThresholdMetric === AVAILABILITY ? _(`${this.props.unhealthyEntityCount} Unavailable`) :
      _(`${this.props.unhealthyEntityCount} Unhealthy`);

    return (
      <div>
        <ControlBar id="nav-top" borderBottom={false}>
          <ToggleButtonGroup
            currentTab={this.props.currentTab}
            onToggle={this.handleToggle}
          />
          {
            this.props.showTimeRangePicker &&
              <div style={this.cssStyles.timeRange}>
                <SplunkwebConnector>
                  <TimeRangeDropdown
                    onChange={this.props.onTimeRangeChange}
                    earliest={this.props.earliestTime}
                    latest="now"
                    appearance="pill"
                    formInputTypes={[]}
                    advancedInputTypes={[]}
                  />
                </SplunkwebConnector>
              </div>
          }
          <div style={this.cssStyles.multiselectGroup}>
            {multiselect}
            <GroupContextControl
              currentGroupContext={this.props.currentGroupContext}
              groupSaveState={this.props.groupSaveState}
              createGroupPopoverMessage={this.props.createGroupPopoverMessage}
              onSaveGroup={this.onSaveGroup}
              onUpdateGroup={this.onUpdateGroup}
              getGroupTitleSuggestion={this.constructGroupTitle}
              isGroupContextDirty={groupContextFilters !== currentFilters}
            />
          </div>
          {confirmationDialog}
        </ControlBar>
        <ControlBar id="nav-bottom" borderBottom>
          <div style={this.cssStyles.navContainer} >
            <div style={this.cssStyles.entityCountStyle}>
              <P id="entity-count" style={{ marginBottom: '0' }}>{ entityCountString }</P>
            </div>
            <div style={this.cssStyles.divider}>
              <Divider />
            </div>
            {this.props.selectedView === TILE_VIEW &&
            [
              <div style={this.cssStyles.colorByStyle} key="div453">
                <Select
                  filter
                  disabled={metricDropdownDisabled}
                  id="colorby-select"
                  appearance="pill"
                  prefixLabel={_('Color by')}
                  value={this.props.entityThresholdMetric}
                  placeholder={_(`Color by: ${this.props.entityThresholdMetric}`)}
                  onChange={this.handleColorByChange}
                >
                  {
                    this.props.metricsList.map(metric => (
                      <Select.Option key={metric} label={_(metric)} value={metric} />
                    ))
                  }
                </Select>
                {this.props.entityThresholdMetric !== AVAILABILITY &&
                <ThresholdControl
                  thresholdEvaluator={this.props.entityThresholdEvaluator}
                  thresholdValueMinimum={this.props.entityThresholdValueMinimum}
                  thresholdValueMaximum={this.props.entityThresholdValueMaximum}
                  thresholdValue={this.props.entityThresholdValue}
                  onUpdateThresholdInfo={this.onThresholdUpdate}
                />
                }
                <div style={this.cssStyles.countByHealthStyle}>
                  <Error width="18px" height="18px" preserveAspectRatio="xMaxYMid" style={this.cssStyles.unhealthyEntity} />
                  <P id="unhealthy-entity-count">{ unhealthyEntityCountString } </P>
                </div>
                <div style={this.cssStyles.countByHealthStyle}>
                  <Success width="18px" height="18px" preserveAspectRatio="xMaxYMid" style={this.cssStyles.healthyEntity} />
                  <P id="healthy-entity-count">{ healthyEntityCountString }</P>
                </div>
              </div>,
              <div style={this.cssStyles.divider} key="divider2">
                <Divider />
              </div>,
            ]
            }
            <div style={this.cssStyles.divider}>
              <Divider />
            </div>
            <div style={this.cssStyles.refreshTextStyle}>
              <RefreshControl
                refreshInterval={this.props.refreshInterval}
                onRefresh={this.props.onRefreshEntities}
                onUpdateRefreshInterval={this.handleUpdateRefreshInterval}
              />
            </div>
            { this.props.entityFetchState === FETCH_OBJECT_STATE.IN_PROGRESS &&
              <WaitSpinner style={this.cssStyles.waitSpinnerStyle} />
            }
          </div>
          <ToggleTileListControl
            selectedView={this.props.selectedView}
            onListTileToggle={this.props.onListTileToggle}
          />
        </ControlBar>
      </div>
    );
  }
}



