import React, { Component } from 'react';
import PropTypes from 'prop-types';
import RadioBar from '@splunk/react-ui/RadioBar';

import List from '@splunk/react-icons/List';
import Tiles from '@splunk/react-icons/Tiles';

import { createTestHook } from '../../util/TestSupport';
import { LIST_VIEW, TILE_VIEW } from '../../util/StateConstants';


export default class ToggleTileListControl extends Component {
  static propTypes = {
    selectedView: PropTypes.string.isRequired,
    onListTileToggle: PropTypes.func.isRequired,
    disabled: PropTypes.bool,
  };

  static defaultProps = {
    disabled: false,
  }

  constructor(props, context) {
    super(props, context);
    this.cssStyles = {
      container: {
        display: 'flex',
        marginRight: '20px',
      },
    };
  }

  handleToggle = (e, { value }) => {
    this.props.onListTileToggle(value);
  };

  render() {
    return (
      <div
        {...createTestHook(__filename, name)}
        style={this.cssStyles.container}
      >
        <RadioBar
          onChange={this.handleToggle}
          value={this.props.disabled ? '' : this.props.selectedView}
          appearance="pill"
        >
          <RadioBar.Option
            id="tile-view"
            value={TILE_VIEW}
            disabled={this.props.disabled}
            icon={
              <Tiles
                width="12px"
                height="12px"
                preserveAspectRatio="xMaxYMid"
              />
            }
          />
          <RadioBar.Option
            id="list-view"
            value={LIST_VIEW}
            disabled={this.props.disabled}
            icon={
              <List
                width="12px"
                height="12px"
                preserveAspectRatio="xMaxYMid"
              />
            }
          />
        </RadioBar>
      </div>
    );
  }
}



