import React from 'react';
import PropTypes from 'prop-types';
import Radium from 'radium';

import Error from '@splunk/react-icons/Error';
import Tooltip from '@splunk/react-ui/Tooltip';

import { createTestHook } from '../../../util/TestSupport';
import EntitiesListPageContents from '../EntitiesListing';
import {
  ENTITY_HEALTH,
  TILE_STYLES_SMALL,
} from './EntityTileConstants';

const EntityTileSmall = class EntityTileSmall extends React.Component {
  static propTypes = {
    /**
     * the entity to be displayed in the tile
     */
    entity: PropTypes.object,
  };

  static defaultProps = {
    entity: {},
  };

  constructor(props) {
    super(props);

    this.styles = { ...TILE_STYLES_SMALL };
  }

  render() {
    const internalKey = `${this.props.entity.name}`;

    return (
      <Tooltip content={this.props.entity.title} style={this.styles.tooltip}>
        <div
          {...createTestHook(__filename, name)}
          role="button"
          tabIndex={0}
          key={internalKey}
          style={{
            ...this.styles.cardBody,
            ...this.styles.entityHealthAttributes[this.props.entity.entityHealth],
          }}
          onClick={() => {
            window.location.href = EntitiesListPageContents.getEntityUrl(this.props.entity);
          }}
          data-test-health={this.props.entity.entityHealth}
          data-test-name={this.props.entity.title}
        >
          {this.props.entity.entityHealth === ENTITY_HEALTH.UNHEALTHY && (
            <Error style={this.styles.errorIcon} />
          )}
        </div>
      </Tooltip>
    );
  }
};

export default Radium(EntityTileSmall);



