import React from 'react';
import PropTypes from 'prop-types';
import { abbreviateNumber } from '@splunk/ui-utils/format';
import { _ } from '@splunk/ui-utils/i18n';
import ControlGroup from '@splunk/react-ui/ControlGroup';
import Button from '@splunk/react-ui/Button';
import Gear from '@splunk/react-icons/Gear';
import Slider from '@splunk/react-ui/Slider';
import Popover from '@splunk/react-ui/Popover';
import Clickable from '@splunk/react-ui/Clickable';
import Number from '@splunk/react-ui/Number';

import { createTestHook } from '../../util/TestSupport';
import { HEALTHY_FLAT, UNHEALTHY_FLAT } from './Colors';
import SwitchDirection from './Icons/SwitchDirection';
import { THRESHOLD_EVALUATOR } from '../../util/StateConstants';


export default class ThresholdControl extends React.Component {
  static propTypes = {
    /**
     * Callback for when the threshold info has been updated
     */
    onUpdateThresholdInfo: PropTypes.func.isRequired,

    /**
     * Minimum threshold value
     */
    thresholdValueMinimum: PropTypes.number.isRequired,

    /**
     * Maximum threshold value
     */
    thresholdValueMaximum: PropTypes.number.isRequired,

    /**
     * The currently set threshold value
     */
    thresholdValue: PropTypes.number.isRequired,

    /**
     * The threshold evaluation function
     */
    thresholdEvaluator: PropTypes.func.isRequired,

    /**
     * The labelFormatter for slider
     */
    labelFormatter: PropTypes.func,
  };

  static defaultProps = {
    labelFormatter: value => (`${abbreviateNumber(value)}`),
  };


  constructor(props, ...rest) {
    super(props, ...rest);
    this.styles = {
      sliderStyle: {
        display: 'inline-block',
        marginLeft: '20px',
        marginRight: '5px',
      },
      thresholdControls: {
        padding: '20px',
        paddingBottom: '10px',
        width: '160px',
      },
      switchControls: {
        padding: '30px 20px 20px 0px',
        width: '24px',
      },
      minColor: () => ({
        width: '24px',
        height: '24px',
        backgroundColor:
          this.props.thresholdEvaluator === THRESHOLD_EVALUATOR.DEFAULT
            ? HEALTHY_FLAT
            : UNHEALTHY_FLAT,
        marginBottom: '13px',
      }),
      maxColor: () => ({
        width: '24px',
        height: '24px',
        backgroundColor:
          this.props.thresholdEvaluator === THRESHOLD_EVALUATOR.DEFAULT
            ? UNHEALTHY_FLAT
            : HEALTHY_FLAT,
        marginTop: '7px',
      }),
    };
    this.state = {
      settingsOpen: false,
      settingsAnchor: null,
    };
  }

  handleMount = (component) => {
    this.setState({
      settingsAnchor: component,
    });
  };

  handleOpen = () => {
    this.setState({
      settingsOpen: true,
    });
  };

  handleRequestClose = () => {
    this.setState({
      settingsOpen: false,
    });
  };

  handleSliderChange = (e, { value }) => {
    this.props.onUpdateThresholdInfo({
      thresholdValue: value,
    });
  };

  /**
   * Updates the minimum threshold value
   * If the threshold is lower than this, then set that as well
   */
  handleMinThresholdChange = (e, { value }) => {
    if (value < this.props.thresholdValueMaximum) {
      const thresholdInfo = {};
      thresholdInfo.thresholdValueMinimum = value;
      if (this.props.thresholdValue < value) {
        thresholdInfo.thresholdValue = value;
      }
      this.props.onUpdateThresholdInfo(thresholdInfo);
    }
  }

  /**
   * Updates the maximum threshold value
   * If the threshold is higher than this, then set that as well
   */
  handleMaxThresholdChange = (e, { value }) => {
    if (value > this.props.thresholdValueMinimum) {
      const thresholdInfo = {};
      thresholdInfo.thresholdValueMaximum = value;
      if (this.props.thresholdValue > value) {
        thresholdInfo.thresholdValue = value;
      }
      this.props.onUpdateThresholdInfo(thresholdInfo);
    }
  }

  /**
   * Updates the threshold value as long as it's within bounds
   */
  handleThresholdValueChange = (e, { value }) => {
    if (value <= this.props.thresholdValueMaximum && value >= this.props.thresholdValueMinimum) {
      this.props.onUpdateThresholdInfo({ thresholdValue: value });
    }
  }

  /**
   * Swaps the evaluator when the threshold direction is switched
   */
  handleSwitch = () => {
    if (this.props.thresholdEvaluator === THRESHOLD_EVALUATOR.DEFAULT) {
      this.props.onUpdateThresholdInfo({ thresholdEvaluator: THRESHOLD_EVALUATOR.INVERSE });
    } else {
      this.props.onUpdateThresholdInfo({ thresholdEvaluator: THRESHOLD_EVALUATOR.DEFAULT });
    }
  }

  render() {
    const minLabel = (
      <span
        style={{ color: this.styles.minColor().backgroundColor }}
      >
        {_(`< ${this.props.labelFormatter(this.props.thresholdValueMinimum)}`)}
      </span>
    );
    const maxLabel = (
      <span
        style={{ color: this.styles.maxColor().backgroundColor }}
      >
        {_(`> ${this.props.labelFormatter(this.props.thresholdValueMaximum)}`)}
      </span>
    );

    // Provide at least 5 steps for the user
    const sliderStep = Math.min(
      (this.props.thresholdValueMaximum - this.props.thresholdValueMinimum) / 5,
      1,
    );

    return (
      <div
        {...createTestHook(__filename, name)}
        style={this.styles.sliderStyle}
      >
        {_('Color by: Percent Available')} &nbsp;&nbsp;&nbsp;
        <Slider
          inline
          min={this.props.thresholdValueMinimum}
          minLabel={minLabel}
          max={this.props.thresholdValueMaximum}
          maxLabel={maxLabel}
          step={sliderStep}
          value={this.props.thresholdValue}
          onChange={this.handleSliderChange}
        />
        <Button
          data-test="threshold-settings"
          icon={<Gear size={1} />}
          appearance="pill"
          onClick={this.handleOpen}
          ref={this.handleMount}
        />
        <Popover
          open={this.state.settingsOpen}
          anchor={this.state.settingsAnchor}
          onRequestClose={this.handleRequestClose}
        >
          <div style={{ display: 'flex' }}>
            <div style={this.styles.thresholdControls}>
              <ControlGroup
                label={_('Minimum')}
                labelWidth={66}
              >
                <Number
                  data-test="threshold-settings-min"
                  value={this.props.thresholdValueMinimum}
                  onChange={this.handleMinThresholdChange}
                  inline
                />
              </ControlGroup>
              <ControlGroup
                label={_('Threshold')}
                labelWidth={66}
              >
                <Number
                  data-test="threshold-settings-value"
                  value={this.props.thresholdValue}
                  onChange={this.handleThresholdValueChange}
                  inline
                />
              </ControlGroup>
              <ControlGroup
                label={_('Maximum')}
                labelWidth={66}
              >
                <Number
                  data-test="threshold-settings-max"
                  value={this.props.thresholdValueMaximum}
                  onChange={this.handleMaxThresholdChange}
                  inline
                />
              </ControlGroup>
            </div>
            <div style={this.styles.switchControls}>
              <div
                data-test="switch-color-min"
                data-test-color={this.props.thresholdEvaluator === THRESHOLD_EVALUATOR.DEFAULT ? 'green' : 'red'}
                style={this.styles.minColor()}
              />
              <Clickable
                data-test="threshold-settings-switch"
                onClick={this.handleSwitch}
              >
                <SwitchDirection />
              </Clickable>
              <div
                data-test="switch-color-max"
                data-test-color={this.props.thresholdEvaluator === THRESHOLD_EVALUATOR.DEFAULT ? 'red' : 'green'}
                style={this.styles.maxColor()}
              />
            </div>
          </div>
        </Popover>
      </div>
    );
  }
}



