import React from 'react';
import PropTypes from 'prop-types';
import Radium from 'radium';

import { abbreviateNumber } from '@splunk/ui-utils/format';
import { _ } from '@splunk/ui-utils/i18n';

import Error from '@splunk/react-icons/Error';

import { createTestHook } from '../../../util/TestSupport';
import GroupsList from '../GroupsListing';
import { GROUP_HEALTH,
  TILE_STYLES_MEDIUM,
  getPercentHealthyEntities,
} from './GroupTileConstants';
import { AVAILABILITY } from '../ColorByOption';

import { truncateTitle } from '../../../util/Investigate';

const GroupTileMedium = class GroupTileMedium extends React.Component {
  static propTypes = {
    /**
     * The entity to be displayed in the tile
     */
    group: PropTypes.object,

    /**
     * If true, displays the threshold data value
     */
    groupThresholdMetric: PropTypes.string,
  };

  static defaultProps = {
    group: {},
    groupThresholdMetric: AVAILABILITY,
  };

  constructor(props) {
    super(props);

    this.styles = { ...TILE_STYLES_MEDIUM };
  }

  getCardHoverState(key) {
    return Radium.getState(this.state, key, ':hover');
  }

  getPercentHealthyEntities() {
    const ratio = this.props.group.numberOfEntities === 0 ? 0 :
      (this.props.group.numActiveEntities / this.props.group.numberOfEntities);
    return (ratio * 100).toFixed();
  }

  render() {
    const internalKey = `${this.props.group.name}`;
    const dimensionValues = this.props.group.dimensions.map(dimension => dimension[1]);

    return (
      <div
        {...createTestHook(__filename, name)}
        role="button"
        tabIndex={0}
        key={internalKey}
        style={{
          ...this.styles.cardBody,
          ...this.styles.groupHealthAttributes[this.props.group.groupHealth],
          ...this.styles.cursorAttributes[this.props.group.groupHealth],
        }}
        onClick={() => {
          if (this.props.group.numberOfEntities > 0) {
            window.location.href = GroupsList.getGroupUrl(this.props.group);
          }
        }}
        data-test-health={this.props.group.groupHealth}
        disabled={this.props.group.numberOfEntities === 0}
      >
        {this.props.group.groupHealth === GROUP_HEALTH.UNHEALTHY && (
          <Error style={this.styles.errorIcon} />
        )}
        <div
          className="title-container"
          style={this.styles.title}
          data-test-name={this.props.group.title}
        >
          {_(`${truncateTitle(this.props.group.title)} (${this.props.group.numberOfEntities})`)}
        </div>
        {(this.props.group.groupHealth === GROUP_HEALTH.UNHEALTHY ||
          this.getCardHoverState(internalKey)) && (
            <div>
              {this.props.groupThresholdMetric !== AVAILABILITY &&
              <span className="metric-value" style={this.styles.lastUpdated}>
                {abbreviateNumber(this.props.group.thresholdDataValue)}
                <span style={this.styles.percentSign}>{_('%')}</span>
              </span>
              }
              {this.props.groupThresholdMetric === AVAILABILITY &&
              <span className="percentOfHealthyEntities" style={this.styles.percentOfHealthyEntities}>
                {_(`${getPercentHealthyEntities(this.props.group.numberOfEntities, this.props.group.numActiveEntities)}`)}
                <span style={this.styles.percentSign}>{_('%')}</span>
              </span>
              }
              <span className="assetInfo" style={this.styles.assetInfo}>
                {dimensionValues.join(', ')}
              </span>
            </div>
          )
        }
      </div>
    );
  }
};

export default Radium(GroupTileMedium);



