import _ from 'lodash';

export function isAllSelected(items, selectedItems, criterion) {
  if (items.length === 0) {
    return false;
  }
  return _.differenceBy(items, selectedItems, criterion).length === 0;
}

export function isSelected(arr, val, criterion) {
  return _.map(arr, criterion).indexOf(val) >= 0;
}

export function updateSelectedItems(selectedItems, itemsToBeToggled, criterion) {
  if (itemsToBeToggled.length === _.intersectionBy(
    selectedItems, itemsToBeToggled, criterion).length) {
    return _.differenceBy(selectedItems, itemsToBeToggled, criterion);
  }
  return _.unionBy(selectedItems, itemsToBeToggled, criterion);
}

/**
* Truncate the entity title if it is longer than 24 characters.
*
* @param {String} entity title
* @returns {String} truncated entity title
*/
export function truncateTitle(title) {
  if (title.length <= 24) {
    return title;
  }
  return `${title.substring(0, 24)}...`;
}

/**
* Extract the dimension value(s) for a matching dimension name.
*
* @param {Array} dimensions
* @param {String} dimension name
* @returns {String} dimension value
*/
export function extractDimensionValue(dimensions, dimensionName) {
  return dimensions
    .filter(x => x[0] === dimensionName)
    .map(x => x[1]);
}

/**
* Get the formatted string for displaying multiple values for an entity tile.
*
* @param {Array} an array of values
* @param {String} a formatted values string
*/
export function formatMultipleValues(values) {
  if (values.length > 1) {
    return `${values[0]}, ... + ${values.length - 1}`;
  }
  return values;
}

/**
 * Format value to percent
 * @param {Number} value to be formatted to percent
 */
export function percentFormatter(value) {
  return `${value}%`;
}



