import {
  GROUP_CREATE,
  START,
  SUCCESS,
  FAILURE,
} from '../actionTypes';
import ActionUtils from '../actionUtils';

// This part will be used for saga
export const GROUP_CREATION_TRACKER = ActionUtils.createRequestTypes(GROUP_CREATE);

export const addNewGroup = {
  start: () => ActionUtils.createAction(GROUP_CREATION_TRACKER[START], {}),
  success: response => ActionUtils.createAction(GROUP_CREATION_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(GROUP_CREATION_TRACKER[FAILURE], { error }),
};

export const createGroup = groupInfo => ActionUtils.createAction(GROUP_CREATE, groupInfo);



