import React from 'react';
import PropTypes from 'prop-types';

class UrlWrapper extends React.Component {
  static propTypes = {
    urlParams: PropTypes.string.isRequired,
    children: PropTypes.oneOfType([
      PropTypes.element,
      PropTypes.arrayOf(PropTypes.element),
    ]),
  };

  static defaultProps = {
    urlParams: '',
    children: [],
  };

  static onBackButtonEvent(e) {
    e.preventDefault();
    location.reload();
  }

  componentDidMount() {
    window.onpopstate = this.onBackButtonEvent;
  }

  componentDidUpdate(prevProps) {
    if (prevProps.urlParams !== this.props.urlParams || location.search.substring(1) === '') {
      const pageUrl = `?${this.props.urlParams}`;
      window.history.pushState('', '', pageUrl);
    }
  }

  render() {
    return (
      <div className="wrapper">
        {this.props.children}
      </div>
    );
  }
}

export default UrlWrapper;



