import React from 'react';
import PropTypes from 'prop-types';
import Radium from 'radium';
import { sortBy } from 'lodash';

import CardLayout from '@splunk/react-ui/CardLayout';

import { TILE_RESOLUTION } from '../../../util/StateConstants';
import { AVAILABILITY } from '../ColorByOption';
import TileLayout from './TileLayout';
import GroupTileMedium from './GroupTileMedium';
import {
  groupHealthToSortValue,
  TILE_STYLES_MEDIUM,
  getPercentHealthyEntities,
} from './GroupTileConstants';

const GroupTileView = class GroupTileView extends React.Component {
  static propTypes = {
    /**
     * List of groups to be displayed
     */
    filteredGroups: PropTypes.array,

    /**
     * The threshold metric name
     */
    groupThresholdMetric: PropTypes.string,
    /**
     * Groups last fetched timestamp
     */
    groupsLastFetched: PropTypes.number,

    /**
     * Callback for refreshing groups
     */
    onRefreshGroups: PropTypes.func.isRequired,
  };

  static defaultProps = {
    filteredGroups: [],
    groupThresholdMetric: AVAILABILITY,
    groupsLastFetched: -1,
  };

  constructor(props) {
    super(props);
    this.styles = { ...TILE_STYLES_MEDIUM };
  }

  componentDidMount() {
    this.props.onRefreshGroups();
  }

  render() {
    const sortedGroupsByActivity = sortBy(
      this.props.filteredGroups,
      group => getPercentHealthyEntities(group.numberOfEntities, group.numActiveEntities),
    );
    const sortedGroupsByHealth = sortBy(
      sortedGroupsByActivity,
      value => groupHealthToSortValue[value.groupHealth],
    );

    const groupTiles = sortedGroupsByHealth.map(group => (<GroupTileMedium
      key={group.name}
      group={group}
      groupThresholdMetric={this.props.groupThresholdMetric}
    />));

    return (
      <TileLayout
        onTileResolutionChange={() => {}}
        tileResolution={TILE_RESOLUTION.MEDIUM}
        supportedResolutions={[TILE_RESOLUTION.SMALL, TILE_RESOLUTION.MEDIUM]}
        dataLastFetched={this.props.groupsLastFetched}
        className="group-tile"
      >
        <CardLayout alignCards="left" {...this.styles.layout}>
          {groupTiles}
        </CardLayout>
      </TileLayout>
    );
  }
};

export default Radium(GroupTileView);



