import { cloneDeep, countBy } from 'lodash';
import { GROUP_RETRIEVE_TRACKER } from '../../actions/groups/GroupRetrieve';
import { GROUP_FILTER_RETRIEVE_TRACKER } from '../../actions/groups/GroupFilterRetrieve';
import * as actions from '../../actions/actionTypes';
import { GROUP_DELETION_TRACKER } from '../../actions/groups/GroupDeletion';
import { LOAD_USER_ROLES_TRACKER } from '../../actions/settings/ServerLogging';
import {
  DELETE_OBJECT_STATE,
  FETCH_OBJECT_STATE,
  GROUP_VIEW_TAB,
  THRESHOLD_EVALUATOR,
} from '../../util/StateConstants';
import { AVAILABILITY } from '../../components/investigate/ColorByOption';
import { computeGroupHealth } from '../../util/Groups';

const defaultState = {
  allGroupTitles: [],
  filteredGroupTitles: [],
  filteredGroups: [],
  groupFilters: [],
  selectedGroups: [],
  groupFetchState: FETCH_OBJECT_STATE.READY,
  groupDeletionState: DELETE_OBJECT_STATE.READY,
  totalGroupCount: 0,
  groupsOffset: 0,
  groupsFetchLimit: 50,
  groupSortKey: 'title',
  groupSortBy: 'asc',
  groupsLastFetched: -1,
  threshold: {
    metric: AVAILABILITY,
    thresholdData: {},
    thresholdValue: 80,
    thresholdValueMinimum: 0,
    thresholdValueMaximum: 100,
    thresholdEvaluator: THRESHOLD_EVALUATOR.INVERSE,
  },
  totalGroupsKVStore: -1,
};

function formatGroups(groupsData) {
  return groupsData.map(group =>
    ({
      name: group.name,
      title: group.content.title,
      dimensions: group.content.filter,
      numberOfEntities: group.content.entities_count || 0,
      numActiveEntities: group.content.active_entities_count || 0,
      numInactiveEntities: group.content.inactive_entities_count || 0,
      numDisabledEntities: group.content.disabled_entities_count || 0,
    }));
}


const groupsReducer = (state = defaultState, action) => {
  let groupsList = [];
  switch (action.type) {
    case GROUP_RETRIEVE_TRACKER.START:
      return { ...state, groupFetchState: FETCH_OBJECT_STATE.IN_PROGRESS };

    case GROUP_RETRIEVE_TRACKER.SUCCESS: {
      const { formattedResponse, totalGroupsKVStore, totalCount, updated } = action.response;
      groupsList = formatGroups(formattedResponse.entry);
      return { ...state,
        totalGroupsKVStore,
        filteredGroups: groupsList,
        groupFetchState: FETCH_OBJECT_STATE.SUCCESS,
        totalGroupCount: totalCount,
        groupsLastFetched: updated,
      };
    }
    case GROUP_RETRIEVE_TRACKER.FAILURE:
      // TODO: Handle error message by modal/message depends on UX
      return state;


    case LOAD_USER_ROLES_TRACKER.START: {
      return { ...state, userRoles: [] };
    }

    case LOAD_USER_ROLES_TRACKER.SUCCESS: {
      return { ...state, userRoles: action.response.roles };
    }

    case LOAD_USER_ROLES_TRACKER.FAILURE: {
      return { ...state, userRoles: [] };
    }

    case actions.GROUPS_LISTING_SORT_GROUPS: {
      const prevSortBy = state.groupSortKey === action.sortKey ? state.groupSortBy : 'none';
      const nextSortBy = prevSortBy === 'asc' ? 'desc' : 'asc';
      return { ...state,
        groupSortKey: action.sortKey,
        groupSortBy: nextSortBy,
      };
    }

    case actions.GROUPS_LISTING_SORT_BY: {
      return {
        ...state,
        groupSortBy: action.sortBy,
      };
    }

    case actions.GROUP_DELETION_CONFIRM: {
      return { ...state,
        groupDeletionState: DELETE_OBJECT_STATE.CONFIRM,
        selectedGroups: action.groups,
      };
    }
    case actions.GROUP_DELETION_READY: {
      return { ...state,
        groupDeletionState: DELETE_OBJECT_STATE.READY,
        selectedGroups: [],
      };
    }
    case actions.GROUP_SELECT: {
      return { ...state, selectedGroups: action.groups };
    }

    case actions.GROUP_SELECT_CLEAR: {
      return { ...state, selectedGroups: [] };
    }

    case GROUP_DELETION_TRACKER.START:
      return { ...state, groupDeletionState: DELETE_OBJECT_STATE.IN_PROGRESS };

    case GROUP_DELETION_TRACKER.SUCCESS: {
      const { formattedResponse, totalGroupsKVStore, totalCount, updated } = action.response;
      groupsList = formatGroups(formattedResponse.entry);
      return { ...state,
        totalGroupsKVStore,
        filteredGroups: groupsList,
        groupFetchState: FETCH_OBJECT_STATE.READY,
        groupDeletionState: DELETE_OBJECT_STATE.SUCCESS,
        selectedGroups: [],
        totalGroupCount: totalCount,
        groupsLastFetched: updated,
      };
    }

    case GROUP_DELETION_TRACKER.FAILURE:
      // TODO: Handle error message by modal/message depends on UX
      return { ...state, groupDeletionState: DELETE_OBJECT_STATE.ERROR };

    case actions.FILTER_GROUPS: {
      const filter = action.filter;
      const allGroupTitlesCopy = cloneDeep(state.allGroupTitles);
      const filteredGroupTitles = allGroupTitlesCopy.filter(x =>
        filter === '' || x.toLowerCase().startsWith(filter.toLowerCase()));
      return { ...state, filteredGroupTitles };
    }

    case actions.UPDATE_GROUP_FILTERS: {
      return { ...state, groupFilters: action.groupFilters, groupsOffset: 0 };
    }

    case GROUP_FILTER_RETRIEVE_TRACKER.START: {
      return { ...state, isLoadingOptions: true };
    }
    case GROUP_FILTER_RETRIEVE_TRACKER.SUCCESS: {
      return { ...state,
        allGroupTitles: action.response.titles,
        filteredGroupTitles: action.response.titles,
        isLoadingOptions: false,
      };
    }
    case GROUP_FILTER_RETRIEVE_TRACKER.FAILURE: {
      if (!action || !action.error || !action.error.message) {
        return { ...state,
          isLoadingOptions: false,
        };
      }
      return state;
    }
    case actions.GROUP_OFFSET_CHANGE: {
      return {
        ...state,
        groupsOffset: action.groupsOffset,
      };
    }

    case actions.FETCH_WITH_OPTIONS: {
      if (action.fetchOptions && action.viewOptions.selectedTab === GROUP_VIEW_TAB) {
        return { ...state,
          groupFilters: action.fetchOptions.groupFilters,
          groupsOffset: action.fetchOptions.offset,
          groupSortKey: action.fetchOptions.sort_key,
          groupSortBy: action.fetchOptions.sort_dir,
        };
      }
      return state;
    }

    case actions.GROUPS_PER_PAGE_CHANGE: {
      return {
        ...state,
        groupsFetchLimit: action.groupsFetchLimit,
      };
    }

    case actions.GROUP_THRESHOLD_INFO_CHANGE: {
      return { ...state,
        threshold: {
          ...state.threshold,
          ...action.threshold,
        },
      };
    }

    case actions.GROUP_HEALTH_COMPUTE: {
      const filteredGroups = state.filteredGroups.map(group => ({
        ...group,
        groupHealth: computeGroupHealth(
          group,
          group.numberOfEntities ? (group.numActiveEntities / group.numberOfEntities) * 100 : -1,
          state.threshold.thresholdValue,
          state.threshold.thresholdEvaluator),
        thresholdDataValue: state.threshold.thresholdData[group.name],
      }));
      const countByHealth = countBy(filteredGroups, 'groupHealth');
      return {
        ...state,
        filteredGroups,
        healthyGroupCount: countByHealth.HEALTHY,
        unhealthyGroupCount: countByHealth.UNHEALTHY,
      };
    }

    default:
      return state;
  }
};

export default groupsReducer;



