import { cloneDeep, includes } from 'lodash';
import { _ } from '@splunk/ui-utils/i18n';
import * as actions from '../../actions/actionTypes';
import { GROUP_CREATION_TRACKER } from '../../actions/groups/GroupCreation';
import { GROUP_UPDATE_TRACKER } from '../../actions/groups/GroupUpdate';
import { GROUP_RETRIEVE_TRACKER } from '../../actions/groups/GroupRetrieve';
import { GROUP_DELETION_TRACKER } from '../../actions/groups/GroupDeletion';
import { METRIC_NAMES_RETRIEVE_TRACKER } from '../../actions/entities/MetricNamesRetrieve';
import {
  SAVE_OBJECT_STATE,
  LIST_VIEW,
  ENTITY_VIEW_TAB,
} from '../../util/StateConstants';


const defaultState = {
  currentTab: ENTITY_VIEW_TAB,
  currentGroupContext: { name: '', title: '', filter: [] },
  needsToRetrieveGroup: true,
  createGroupPopoverMessage: '',
  groupSaveState: SAVE_OBJECT_STATE.READY,
  selectedView: LIST_VIEW,
  refreshInterval: -1,
};

const controlBarReducer = (state = defaultState, action) => {
  switch (action.type) {
    case actions.INVESTIGATE_TOGGLE: {
      return { ...state, currentTab: action.selectedTab };
    }
    case actions.FETCH_WITH_OPTIONS: {
      if (action.viewOptions) {
        const currentTab = action.viewOptions.selectedTab;
        const selectedView = action.viewOptions.selectedView;
        return { ...state, currentTab, selectedView };
      }
      return state;
    }
    case actions.TILE_LIST_TOGGLE: {
      return { ...state, selectedView: action.selectedView };
    }
    case actions.GROUP_EDIT: {
      return {
        ...state,
        currentGroupContext: cloneDeep(action.currentGroupContext),
        currentTab: ENTITY_VIEW_TAB,
      };
    }
    case actions.UPDATE_REFRESH_INTERVAL: {
      return {
        ...state,
        refreshInterval: action.interval,
      };
    }
    case METRIC_NAMES_RETRIEVE_TRACKER.SUCCESS: {
      return { ...state, metricsList: Object.keys(action.response) };
    }
    case GROUP_RETRIEVE_TRACKER.SUCCESS:
      // when group retrieve is successful
      return { ...state, needsToRetrieveGroup: false };
    case GROUP_CREATION_TRACKER.START:
    case GROUP_UPDATE_TRACKER.START:
      // change group creation state to IN_PROGRESS
      return {
        ...state,
        createGroupPopoverMessage: '',
        groupSaveState: SAVE_OBJECT_STATE.IN_PROGRESS,
      };
    case GROUP_CREATION_TRACKER.SUCCESS:
    case GROUP_UPDATE_TRACKER.SUCCESS: {
      // change group creation state to SUCCESS
      const groupResponse = action.response.entry[0];
      const currentGroupContext = {
        name: groupResponse.name,
        title: groupResponse.content.title,
        filter: groupResponse.content.filter,
      };
      return {
        ...state,
        groupSaveState: SAVE_OBJECT_STATE.SUCCESS,
        currentGroupContext,
        needsToRetrieveGroup: true,
      };
    }
    case GROUP_CREATION_TRACKER.FAILURE:
    case GROUP_UPDATE_TRACKER.FAILURE: {
      if (!action || !action.error || !action.error.message) {
        // change group creation state to ERROR and set error message
        return {
          ...state,
          createGroupPopoverMessage: _('Failed to save group'),
          groupSaveState: SAVE_OBJECT_STATE.ERROR,
        };
      }
      return {
        ...state,
        createGroupPopoverMessage: _(`Failure: ${action.error.message}`),
        groupSaveState: SAVE_OBJECT_STATE.ERROR,
      };
    }
    case GROUP_DELETION_TRACKER.SUCCESS: {
      let currentGroupContext = { name: undefined, title: undefined, filter: [] };
      const { formattedResponse, totalCount } = action.response;
      const remainingGroups = formattedResponse.entry.map(group => group.name);
      const currentGroupContextName = state.currentGroupContext.name;
      if (includes(remainingGroups, currentGroupContextName)) {
        currentGroupContext = state.currentGroupContext;
      }
      return {
        ...state,
        currentGroupContext,
        totalGroupCount: totalCount,
      };
    }
    case actions.GROUP_SAVE_SUCCESS_DISMISS:
      // when the group success dialog is closed new groups can be created.
      return {
        ...state,
        createGroupPopoverMessage: '',
        groupSaveState: SAVE_OBJECT_STATE.READY,
      };
    default:
      return state;
  }
};

export default controlBarReducer;



