import { put, takeLatest, call } from 'redux-saga/effects';
import { restartSplunk, loadUserRoles } from '../services/serverLogging';
import { RESTART_SPLUNK, LOAD_USER_ROLES, TILE_LIST_TOGGLE, INVESTIGATE_TOGGLE } from '../actions/actionTypes';
import { restartTracker, loadUserRolesTracker } from '../actions/settings/ServerLogging';


export function* restartSplunkInstance() {
  yield put(restartTracker.start());
  try {
    const response = yield call(restartSplunk);
    if (response && response.results && response.results.length > 1) {
      yield put(restartTracker.success());
    } else {
      yield put(restartTracker.failure());
    }
  } catch (error) {
    yield put(restartTracker.failure());
  }
}

export function* loadRoles() {
  yield put(loadUserRolesTracker.start());
  try {
    const { response } = yield call(loadUserRoles);
    if (response && response.entry && response.entry.length > 0) {
      const roles = response.entry[0].content.roles;
      yield put(loadUserRolesTracker.success({ roles }));
    } else {
      yield put(loadUserRolesTracker.failure());
    }
  } catch (error) {
    yield put(loadUserRolesTracker.failure());
  }
}

export default function* serverLoggingSaga() {
  yield takeLatest(RESTART_SPLUNK, restartSplunkInstance);
  yield takeLatest(LOAD_USER_ROLES, loadRoles);
  yield takeLatest(TILE_LIST_TOGGLE, loadRoles);
  yield takeLatest(INVESTIGATE_TOGGLE, loadRoles);
}



