import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { WHITE_BACKGROUND } from './Colors';

export default class ControlBar extends Component {
  static propTypes = {
    id: PropTypes.string,
    borderBottom: PropTypes.bool,
    children: PropTypes.node,
  }

  static defaultProps = {
    id: '',
    borderBottom: true,
    children: null,
  }

  constructor(props) {
    super(props);

    this.styles = {
      container: {
        backgroundColor: WHITE_BACKGROUND,
        paddingTop: '5px',
        minHeight: '40px',
        width: '100%',
        display: 'flex',
        alignItems: 'center',
      },
    };

    if (this.props.borderBottom === true) {
      this.styles.container.borderBottom = '1px solid #ddd';
    }
  }

  render() {
    return (
      <div id={this.props.id} style={this.styles.container}>
        {this.props.children}
      </div>
    );
  }
}



