import React from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import pick from 'lodash/pick';

import { sortEntities,
  entityDeletionConfirm,
  entityDeletionReady,
  entityOffsetChange,
  entityTileResolutionChange,
  entityThresholdInfoChange,
  entitiesPerPageChange,
} from '../../actions/investigate/EntitiesListing';
import { loadUserRoles } from '../../actions/settings/ServerLogging';

import { filterDimensions,
  updateSelectedDimensions,
} from '../../actions/common/DimensionMultiSelect';
import { loadEntities } from '../../actions/entities/EntityRetrieve';
import { loadMetricNames } from '../../actions/entities/MetricNamesRetrieve';
import { loadDimensions } from '../../actions/entities/DimensionRetrieve';
import { createGroup } from '../../actions/groups/GroupCreation';
import { updateGroup } from '../../actions/groups/GroupUpdate';
import { loadGroups } from '../../actions/groups/GroupRetrieve';
import { loadGroupFilter } from '../../actions/groups/GroupFilterRetrieve';
import { groupSelect, clearSelectedGroups } from '../../actions/groups/GroupSelection';
import { entitySelect, clearSelectedEntities } from '../../actions/entities/EntitySelection';
import { deleteGroups } from '../../actions/groups/GroupDeletion';
import { deleteEntities } from '../../actions/entities/EntityDeletion';
import tileListToggle from '../../actions/investigate/ToggleTileListControl';
import {
  toggle,
  editGroup,
  groupSaveSuccessDismiss,
  selectEntityColorBy,
} from '../../actions/investigate/ControlBar';
import updateRefreshInterval from '../../actions/common/Refresh';
import { refreshEntities } from '../../actions/entities/EntityRefresh';
import { computeEntitiesHealth } from '../../actions/entities/EntityHealthCompute';
import { computeGroupsHealth } from '../../actions/groups/GroupHealthCompute';
import { refreshGroup } from '../../actions/groups/GroupRefresh';
import {
  groupDeletionConfirm,
  groupDeletionReady,
  filterGroups,
  updateGroupFilters,
  sortGroups,
  groupOffsetChange,
  groupsPerPageChange,
  groupThresholdChange,
} from '../../actions/investigate/GroupsListing';
import InvestigatePageContents from '../../components/investigate/Investigate';
import UrlWrapper from '../common/UrlWrapper';

/**
 * Container for Investigate page.
 * A very inelegant solution but this is the only way we
 * can get sagas to trigger on component mount.
 *
 * @class InvestigateContainer
 * @extends {React.Component}
 */
class InvestigateContainer extends React.Component {
  static propTypes = {
    ...InvestigatePageContents.propTypes,
    ...UrlWrapper.propTypes,
    // Container-only props
    loadDimensions: PropTypes.func.isRequired,
  }

  static defaultProps = { ...InvestigatePageContents.defaultProps }

  render() {
    const investigateProps = pick(this.props, Object.keys(InvestigatePageContents.propTypes));
    const urlProps = pick(this.props, Object.keys(UrlWrapper.propTypes));
    return (
      <UrlWrapper {...urlProps}>
        <InvestigatePageContents
          {...investigateProps}
        />
      </UrlWrapper>
    );
  }
}

const mapStatesToProps = state => ({
  // Objects to be displayed
  filteredEntities: state.entitiesReducer.filteredEntities,
  filteredGroups: state.groupsReducer.filteredGroups,

  // Roles for current authenticated user
  userRoles: state.entitiesReducer.userRoles,

  // Sorting props
  entitySortKey: state.entitiesReducer.entitySortKey,
  entitySortBy: state.entitiesReducer.entitySortBy,
  groupSortKey: state.groupsReducer.groupSortKey,
  groupSortBy: state.groupsReducer.groupSortBy,

  // Dimension filtering props
  selectedDimensions: state.dimensionMultiselectReducer.selectedDimensions,

  // Group filtering props
  filteredGroupTitles: state.groupsReducer.filteredGroupTitles,
  groupFilters: state.groupsReducer.groupFilters,

  // Fetch state props
  entityFetchState: state.entitiesReducer.entityFetchState,
  dimensionsFetchState: state.dimensionMultiselectReducer.dimensionsFetchState,
  groupFetchState: state.groupsReducer.groupFetchState,

  // Control bar props
  currentGroupContext: state.controlBarReducer.currentGroupContext,
  currentTab: state.controlBarReducer.currentTab,
  refreshInterval: state.controlBarReducer.refreshInterval,
  groupSaveState: state.controlBarReducer.groupSaveState,
  createGroupPopoverMessage: state.controlBarReducer.createGroupPopoverMessage,
  needsToRetrieveGroup: state.controlBarReducer.needsToRetrieveGroup,
  metricsList: state.controlBarReducer.metricsList,
  selectedView: state.controlBarReducer.selectedView,
  metricNameFetchState: state.entitiesReducer.metricNameFetchState,
  metricDataFetchState: state.entitiesReducer.metricDataFetchState,

  // Deletion state props
  groupDeletionState: state.groupsReducer.groupDeletionState,
  entityDeletionState: state.entitiesReducer.entityDeletionState,

  // Selection props
  selectedGroups: state.groupsReducer.selectedGroups,
  selectedEntities: state.entitiesReducer.selectedEntities,

  // totalCount props
  totalEntityCount: state.entitiesReducer.totalEntityCount,
  healthyEntityCount: state.entitiesReducer.healthyEntityCount,
  unhealthyEntityCount: state.entitiesReducer.unhealthyEntityCount,
  totalGroupCount: state.groupsReducer.totalGroupCount,

  // isEntitiesEmpty props
  totalEntitiesKVStore: state.entitiesReducer.totalEntitiesKVStore,
  totalGroupsKVStore: state.groupsReducer.totalGroupsKVStore,

  // last updated prop
  entitiesLastFetched: state.entitiesReducer.entitiesLastFetched,
  groupsLastFetched: state.groupsReducer.groupsLastFetched,

  // Page offset props
  entitiesOffset: state.entitiesReducer.entitiesOffset,
  entitiesPerPage: state.entitiesReducer.entitiesFetchLimit,
  groupsOffset: state.groupsReducer.groupsOffset,
  groupsPerPage: state.groupsReducer.groupsFetchLimit,

  // Tile view and threshold data
  entityTileResolution: state.entitiesReducer.tileResolution,
  entityThresholdMetric: state.entitiesReducer.threshold.metric,
  entityThresholdData: state.entitiesReducer.threshold.thresholdData,
  entityThresholdValue: state.entitiesReducer.threshold.thresholdValue,
  entityThresholdValueMinimum: state.entitiesReducer.threshold.thresholdValueMinimum,
  entityThresholdValueMaximum: state.entitiesReducer.threshold.thresholdValueMaximum,
  entityThresholdEvaluator: state.entitiesReducer.threshold.thresholdEvaluator,
  groupThresholdMetric: state.groupsReducer.threshold.metric,
  groupThresholdData: state.groupsReducer.threshold.thresholdData,
  groupThresholdValue: state.groupsReducer.threshold.thresholdValue,
  groupThresholdValueMinimum: state.groupsReducer.threshold.thresholdValueMinimum,
  groupThresholdValueMaximum: state.groupsReducer.threshold.thresholdValueMaximum,
  groupThresholdEvaluator: state.groupsReducer.threshold.thresholdEvaluator,

  // urlProps
  urlParams: state.urlReducer.urlParams,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  // Pre-mount actions
  loadEntities,
  loadDimensions,
  loadGroups,
  loadMetricNames,
  loadGroupFilter,
  loadRoles: loadUserRoles,

  // Filtering actions
  onDimensionFilterChange: filterDimensions,
  onDimensionSelectionChange: updateSelectedDimensions,
  onGroupFilterChange: filterGroups,
  onGroupSelectionChange: updateGroupFilters,

  // Sorting actions
  onEntitySort: sortEntities,
  onGroupSort: sortGroups,

  // Group CRUD actions
  onCreateGroup: createGroup,
  onUpdateGroup: updateGroup,
  onEditGroup: editGroup,
  onDeleteGroups: deleteGroups,
  onGroupDeletionConfirm: groupDeletionConfirm,
  onGroupDeletionReady: groupDeletionReady,
  handleGroupSuccessDialogClose: groupSaveSuccessDismiss,
  onRefreshGroups: refreshGroup,
  onComputeGroupsHealth: computeGroupsHealth,

  // Entity CRUD actions
  onDeleteEntities: deleteEntities,
  onEntityDeletionConfirm: entityDeletionConfirm,
  onEntityDeletionReady: entityDeletionReady,
  onRefreshEntities: refreshEntities,
  onComputeEntitiesHealth: computeEntitiesHealth,

  // Control bar actions
  onToggle: toggle,
  onUpdateRefreshInterval: updateRefreshInterval,

  // Tile list toggle actions
  onListTileToggle: tileListToggle,
  onEntityTileResolutionChange: entityTileResolutionChange,

  // Selection actions
  onGroupSelection: groupSelect,
  onEntitySelection: entitySelect,
  onClearSelectedGroups: clearSelectedGroups,
  onClearSelectedEntities: clearSelectedEntities,

  // Pagination actions
  onEntityOffsetChange: entityOffsetChange,
  onGroupOffsetChange: groupOffsetChange,

  // Thresholding actions
  onSelectEntityThresholdMetric: selectEntityColorBy,
  onUpdateEntityThresholdInfo: entityThresholdInfoChange,
  onUpdateGroupThresholdInfo: groupThresholdChange,

  // Items per page Actions
  onEntitiesPerPageChange: entitiesPerPageChange,
  onGroupsPerPageChange: groupsPerPageChange,

  // Empty group state view button click actions
  onViewEntitiesClick: toggle,
}, dispatch);

export default connect(
  mapStatesToProps,
  mapDispatchToProps,
)(InvestigateContainer);



