import {
  GROUP_HEALTH_COMPUTE,
} from '../actionTypes';

/**
 * Action to compute group health based on threshold data for group tile view.
 *
 * @export - computeGroupsHealth
 * @returns - an action object
 */
export const computeGroupsHealth = () => ({
  type: GROUP_HEALTH_COMPUTE,
});

export default computeGroupsHealth;



