import {
  GROUP_REFRESH,
} from '../actionTypes';

/**
 * Action to refresh group table and filter groups based on selectedDimensions.
 *
 * @export
 * @returns - an action object
 */
export function refreshGroup() {
  return {
    type: GROUP_REFRESH,
  };
}

export default refreshGroup;



