import { combineReducers, createStore, applyMiddleware } from 'redux';
import createSagaMiddleware, { END } from 'redux-saga';
import entitiesReducer from '../reducers/investigate/Entities';
import groupsReducer from '../reducers/investigate/Groups';
import controlBarReducer from '../reducers/investigate/ControlBar';
import urlReducer from '../reducers/common/UrlReducer';
import dimensionMultiselectReducer from '../reducers/common/DimensionMultiSelect';
import { addReduxLogger } from './util';

export default function investigateStore(initialState) {
  const sagaMiddleware = createSagaMiddleware();
  const middleware = addReduxLogger([sagaMiddleware]);
  const store = createStore(
    combineReducers({
      controlBarReducer,
      entitiesReducer,
      groupsReducer,
      urlReducer,
      dimensionMultiselectReducer,
    }),
    initialState,
    applyMiddleware(...middleware),
  );

  store.runSaga = sagaMiddleware.run;
  store.close = () => store.dispatch(END);
  return store;
}



