import { countBy } from 'lodash';
import * as actions from '../../actions/actionTypes';
import { ENTITY_RETRIEVE_TRACKER } from '../../actions/entities/EntityRetrieve';
import { ENTITY_DELETION_TRACKER } from '../../actions/entities/EntityDeletion';
import { LOAD_USER_ROLES_TRACKER } from '../../actions/settings/ServerLogging';
import {
  FETCH_OBJECT_STATE,
  DELETE_OBJECT_STATE,
  TILE_RESOLUTION,
  THRESHOLD_EVALUATOR,
  TILE_VIEW,
  ENTITY_VIEW_TAB,
} from '../../util/StateConstants';
import { METRIC_NAMES_RETRIEVE_TRACKER } from '../../actions/entities/MetricNamesRetrieve';
import { METRIC_DATA_RETRIEVE_TRACKER } from '../../actions/entities/MetricDataRetrieve';
import {
  addDimensionToEntities,
  computeEntityHealth,
} from '../../util/Entities';
import { AVAILABILITY } from '../../components/investigate/ColorByOption';


const defaultState = {
  filteredEntities: [],
  selectedEntities: [],
  userRoles: [],
  entitySortKey: 'title',
  entitySortBy: 'asc',
  totalEntityCount: 0,
  healthyEntityCount: 0,
  unhealthyEntityCount: 0,
  entitiesOffset: 0,
  entitiesFetchLimit: 50,
  entitiesLastFetched: -1,
  tileResolution: TILE_RESOLUTION.MEDIUM,
  threshold: {
    metric: AVAILABILITY,
    thresholdData: {},
    thresholdValue: 1,
    thresholdValueMinimum: 0,
    thresholdValueMaximum: 2,
    thresholdEvaluator: THRESHOLD_EVALUATOR.DEFAULT,
  },
  metricMetadata: { [AVAILABILITY]: { min: 0, max: 2 } },

  // State trackers
  entityDeletionState: DELETE_OBJECT_STATE.READY,
  entityFetchState: FETCH_OBJECT_STATE.READY,
  metricNameFetchState: FETCH_OBJECT_STATE.READY,
  metricDataFetchState: FETCH_OBJECT_STATE.READY,
  totalEntitiesKVStore: -1,
};

const entitiesReducer = (state = defaultState, action) => {
  switch (action.type) {
    case ENTITY_RETRIEVE_TRACKER.START: {
      return { ...state, entityFetchState: FETCH_OBJECT_STATE.IN_PROGRESS };
    }

    case ENTITY_RETRIEVE_TRACKER.SUCCESS: {
      if (action.response) {
        const { data, totalEntitiesKVStore, totalCount, updated } = action.response;
        const filteredEntities = addDimensionToEntities(data);
        return {
          ...state,
          filteredEntities,
          totalEntitiesKVStore,
          totalEntityCount: totalCount,
          entityFetchState: FETCH_OBJECT_STATE.SUCCESS,
          entitiesLastFetched: updated,
        };
      }
      return state;
    }

    case ENTITY_RETRIEVE_TRACKER.FAILURE:
      return { ...state, entityFetchState: FETCH_OBJECT_STATE.ERROR };

    case METRIC_DATA_RETRIEVE_TRACKER.START: {
      return { ...state, metricDataFetchState: FETCH_OBJECT_STATE.IN_PROGRESS };
    }

    case METRIC_DATA_RETRIEVE_TRACKER.SUCCESS: {
      return {
        ...state,
        threshold: {
          ...state.threshold,
          thresholdData: action.response,
        },
        metricDataFetchState: FETCH_OBJECT_STATE.SUCCESS,
      };
    }

    case METRIC_DATA_RETRIEVE_TRACKER.FAILURE: {
      return { ...state, metricDataFetchState: FETCH_OBJECT_STATE.ERROR };
    }

    case LOAD_USER_ROLES_TRACKER.START: {
      return { ...state, userRoles: [] };
    }

    case LOAD_USER_ROLES_TRACKER.SUCCESS: {
      return { ...state, userRoles: action.response.roles };
    }

    case LOAD_USER_ROLES_TRACKER.FAILURE: {
      return { ...state, userRoles: [] };
    }

    case actions.ENTITIES_LISTING_SORT_ENTITIES: {
      const prevSortBy = state.entitySortKey === action.sortKey ? state.entitySortBy : 'none';
      const nextSortBy = prevSortBy === 'asc' ? 'desc' : 'asc';
      return {
        ...state,
        entitySortKey: action.sortKey,
        entitySortBy: nextSortBy,
      };
    }

    case actions.ENTITY_DELETION_CONFIRM: {
      return {
        ...state,
        entityDeletionState: DELETE_OBJECT_STATE.CONFIRM,
        selectedEntities: action.entities,
      };
    }

    case actions.ENTITY_DELETION_READY: {
      return {
        ...state,
        entityDeletionState: DELETE_OBJECT_STATE.READY,
        selectedEntities: [],
      };
    }

    case actions.ENTITY_SELECT: {
      return { ...state, selectedEntities: action.entities };
    }

    case actions.ENTITY_SELECT_CLEAR: {
      return { ...state, selectedEntities: [] };
    }

    case ENTITY_DELETION_TRACKER.START: {
      return { ...state, entityDeletionState: DELETE_OBJECT_STATE.IN_PROGRESS };
    }

    case ENTITY_DELETION_TRACKER.SUCCESS: {
      let newState = { ...state };
      if (action.response) {
        const { data, totalEntitiesKVStore, totalCount, updated } = action.response;
        const filteredEntities = addDimensionToEntities(data);
        newState = {
          ...state,
          entityDeletionState: DELETE_OBJECT_STATE.SUCCESS,
          totalEntitiesKVStore,
          totalEntityCount: totalCount,
          selectedEntities: [],
          filteredEntities,
          entitiesLastFetched: updated,
        };
      }
      return newState;
    }

    case ENTITY_DELETION_TRACKER.FAILURE: {
      return { ...state, entityDeletionState: DELETE_OBJECT_STATE.ERROR };
    }

    case METRIC_NAMES_RETRIEVE_TRACKER.START: {
      return { ...state, metricNameFetchState: FETCH_OBJECT_STATE.IN_PROGRESS };
    }

    case METRIC_NAMES_RETRIEVE_TRACKER.SUCCESS: {
      return {
        ...state,
        metricMetadata: { ...action.response, ...defaultState.metricMetadata },
        metricNameFetchState: FETCH_OBJECT_STATE.SUCCESS,
      };
    }

    case METRIC_NAMES_RETRIEVE_TRACKER.FAILURE: {
      return { ...state, metricNameFetchState: FETCH_OBJECT_STATE.ERROR };
    }

    case actions.SELECT_ENTITY_COLOR_BY: {
      const metricName = action.selectedOption;
      const { min, max } = state.metricMetadata[metricName];
      const metricChanged = state.threshold.metric !== metricName;
      return { ...state,
        threshold: {
          ...state.threshold,
          metric: metricName,
          thresholdValue: metricChanged ? Math.floor((min + max) / 2) :
            state.threshold.thresholdValue,
          thresholdValueMinimum: min,
          thresholdValueMaximum: max,
          thresholdEvaluator: THRESHOLD_EVALUATOR.DEFAULT,
        },
      };
    }

    case actions.SET_ENTITY_THRESHOLD_DATA: {
      return { ...state,
        threshold: {
          ...state.threshold,
          thresholdData: action.data,
        },
      };
    }
    case actions.ENTITY_THRESHOLD_INFO_CHANGE: {
      return { ...state,
        threshold: {
          ...state.threshold,
          ...action.threshold,
        },
      };
    }

    case actions.ENTITY_OFFSET_CHANGE: {
      return {
        ...state,
        entitiesOffset: action.entitiesOffset,
      };
    }

    case actions.TILE_LIST_TOGGLE: {
      if (action.selectedView === TILE_VIEW) {
        return { ...state, entitiesOffset: 0, entitiesFetchLimit: -1 };
      }

      return { ...state, entitiesFetchLimit: defaultState.entitiesFetchLimit };
    }

    case actions.ENTITY_TILE_RESOLUTION_CHANGE: {
      return { ...state, tileResolution: action.tileResolution };
    }

    case actions.ADD_DIMENSION_SELECTION:
    case actions.UPDATE_SELECTED_DIMENSIONS: {
      return { ...state, entitiesOffset: 0 };
    }

    case actions.FETCH_WITH_OPTIONS: {
      if (action.fetchOptions && action.viewOptions.selectedTab === ENTITY_VIEW_TAB) {
        return { ...state,
          entitiesOffset: action.fetchOptions.offset,
          entitySortKey: action.fetchOptions.sort_key,
          entitySortBy: action.fetchOptions.sort_dir,
        };
      }
      return state;
    }

    case actions.ENTITY_PER_PAGE_CHANGE: {
      return {
        ...state,
        entitiesFetchLimit: action.entitiesFetchLimit,
      };
    }

    case actions.ENTITY_HEALTH_COMPUTE: {
      const filteredEntities = state.filteredEntities.map(entity => ({
        ...entity,
        entityHealth: computeEntityHealth(
          entity,
          state.threshold.thresholdData[entity.name],
          state.threshold.thresholdValue,
          state.threshold.thresholdEvaluator),
        thresholdDataValue: state.threshold.thresholdData[entity.name],
      }));
      const countByHealth = countBy(filteredEntities, 'entityHealth');
      return {
        ...state,
        filteredEntities,
        healthyEntityCount: countByHealth.HEALTHY,
        unhealthyEntityCount: countByHealth.UNHEALTHY,
      };
    }

    default:
      return state;
  }
};

export default entitiesReducer;



