import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Button from '@splunk/react-ui/Button';
import Clickable from '@splunk/react-ui/Clickable';
import Popover from '@splunk/react-ui/Popover';
import Text from '@splunk/react-ui/Text';
import StaticContent from '@splunk/react-ui/StaticContent';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import ControlGroup from '@splunk/react-ui/ControlGroup';
import { _ } from '@splunk/ui-utils/i18n';

import GroupsIcon from '../common/Icons/Groups';

import { SAVE_OBJECT_STATE } from '../../util/StateConstants';
import { ICON_SELECTED } from '../common/Colors';

export default class GroupContextControl extends Component {
  static propTypes = {
    /**
     * The current group context
     */
    currentGroupContext: PropTypes.object,

    /**
     * A string representing the current state of group creation. The values
     * should be one of (READY,IN_PROGRESS, SUCCESS, ERROR)
     * @type {String}
     */
    groupSaveState: PropTypes.oneOf(Object.values(SAVE_OBJECT_STATE)),

    /**
     * Error message for group save failure.
     * @type {string}
     */
    createGroupPopoverMessage: PropTypes.string,

    /**
     * Callback when the group save button is clicked
     * Arguments:
     *    newName - contains the group context: the name and dimensions
     * Returns:
     *    None
     */
    onSaveGroup: PropTypes.func.isRequired,

    /**
     * Callback when the group Update button is clicked
     * Arguments:
     *    newName - contains the group context: the name and dimensions
     * Returns:
     *    None
     */
    onUpdateGroup: PropTypes.func.isRequired,

    /**
     * Callback to get a group title suggestion
     */
    getGroupTitleSuggestion: PropTypes.func.isRequired,

    /**
     * True if the group context is no longer in sync with current selections
     */
    isGroupContextDirty: PropTypes.bool,

  }

  static defaultProps = {
    currentGroupContext: { name: undefined, title: undefined, filter: [] },
    groupSaveState: SAVE_OBJECT_STATE.READY,
    createGroupPopoverMessage: '',
    isGroupContextDirty: true,
  }

  constructor(props, context) {
    super(props, context);

    this.cssStyles = {
      groupContextStyle: {
        display: 'inherit',
        height: '30px',
        border: '1px solid #ccc',
        borderLeft: '1px dotted #ccc',
        borderTopRightRadius: '4px',
        borderBottomRightRadius: '4px',
        paddingLeft: '5px',
        paddingRight: '5px',
      },
      groupPopover: {
        padding: '10px',
        width: '270px',
      },
      createGroupWaitSpinner: {
        marginTop: '10px',
      },
      buttonControl: {
        float: 'right',
        marginLeft: '10px',
      },
      textStyle: () => ({
        color: '#999',
        fontStyle: this.props.isGroupContextDirty ? 'italic' : 'normal',
      }),
    };

    // Keeping this state as internal since this exposes the mechanism
    // of modifying the group details
    this.state = {
      groupModifyAnchor: null,
      groupModifyOpen: false,
      newGroupTitle: '',
    };

    this.onGroupRequest = this.onGroupRequest.bind(this);
  }

  componentWillReceiveProps(newProps) {
    // On group save success we can close the create group dialog.
    if (newProps.groupSaveState === SAVE_OBJECT_STATE.SUCCESS) {
      this.setState({
        groupModifyOpen: false,
      });
    }
  }

  /**
   * Sets internal state for new group title
   * @memberOf ControlBar
   */
  onGroupTitleChange = (e, { value }) => {
    this.setState({
      newGroupTitle: value,
    });
  };


  /**
   * Request change on group
   * @memberOf GroupContextControl
   */
  onGroupRequest(action = 'CREATE') {
    // TODO: Naive validation, need to check more
    if (this.state.newGroupTitle &&
      (this.props.groupSaveState === SAVE_OBJECT_STATE.READY ||
        this.props.groupSaveState === SAVE_OBJECT_STATE.ERROR)) {
      switch (action) {
        case 'CREATE':
          this.props.onSaveGroup(this.state.newGroupTitle);
          break;
        case 'UPDATE':
          this.props.onUpdateGroup(this.state.newGroupTitle);
          break;
        default:
      }
    }
  }

  /**
   * Construct popover component
   * @memberOf GroupContextControl
   */
  getGroupPopover() {
    let label = _('Create group from filter');
    const disableSave = this.props.groupSaveState ===
      SAVE_OBJECT_STATE.IN_PROGRESS || !this.state.newGroupTitle;
    const saveInProgress = this.props.groupSaveState === SAVE_OBJECT_STATE.IN_PROGRESS;

    const waitSpinner = (<WaitSpinner
      style={{
        ...this.cssStyles.createGroupWaitSpinner,
        ...this.cssStyles.buttonControl,
      }}
    />);
    let controlGroup = (
      <ControlGroup label="" labelPosition="left" labelWidth="0" controlsLayout="none">
        <Button
          appearance="primary"
          onClick={() => this.onGroupRequest('CREATE')}
          style={this.cssStyles.buttonControl}
          disabled={disableSave}
          label={_('Save')}
          data-test="submit-group-creation"
        />
        {saveInProgress && waitSpinner}
      </ControlGroup>
    );

    if (this.props.currentGroupContext.name) {
      label = _('Update this group');
      controlGroup = (
        <ControlGroup label="" labelPosition="left" labelWidth="0" controlsLayout="none">
          <Button
            appearance="primary"
            onClick={() => this.onGroupRequest('UPDATE')}
            style={this.cssStyles.buttonControl}
            disabled={disableSave}
            label={_('Update')}
            data-test="submit-group-update"
          />
          <Button
            onClick={() => this.onGroupRequest('CREATE')}
            style={this.cssStyles.buttonControl}
            disabled={disableSave}
            label={_('Save as new group')}
            data-test="submit-group-saveas"
          />
          {saveInProgress && waitSpinner}
        </ControlGroup>
      );
    }

    return (<Popover
      open={this.state.groupModifyOpen}
      anchor={this.state.groupModifyAnchor}
      onRequestClose={this.handleRequestClose}
      data-test="group-popover"
      appearance="light"
    >
      <div style={this.cssStyles.groupPopover}>
        <ControlGroup
          label={label}
          help={this.props.createGroupPopoverMessage}
          labelPosition="top"
          controlsLayout="none"
          error={this.props.groupSaveState === SAVE_OBJECT_STATE.ERROR}
        >
          <Text
            value={this.state.newGroupTitle}
            onChange={this.onGroupTitleChange}
            error={this.props.groupSaveState === SAVE_OBJECT_STATE.ERROR}
            disabled={this.props.groupSaveState === SAVE_OBJECT_STATE.IN_PROGRESS}
            canClear
          />
        </ControlGroup>
        { controlGroup }
      </div>
    </Popover>);
  }
  /**
   * Sets internal state for popover to anchor to
   * @memberOf GroupContextControl
   */
  handleMount = (component) => {
    this.setState({
      groupModifyAnchor: component,
    });
  }

  /**
   * Sets internal state to open popover
   * @memberOf GroupContextControl
   */
  handleRequestOpen = () => {
    this.setState({
      groupModifyOpen: true,
      newGroupTitle: this.props.currentGroupContext.title ||
        this.props.getGroupTitleSuggestion(),
    });
  };

  /**
   * Sets internal state to close popover
   * @memberOf GroupContextControl
   */
  handleRequestClose = () => {
    this.setState({
      groupModifyOpen: false,
      newGroupTitle: '',
    });
  };

  render() {
    const groupPopOver = this.getGroupPopover();

    return (
      <div style={this.cssStyles.groupContextStyle}>
        {this.props.currentGroupContext.title &&
          <StaticContent
            data-test="group-context"
            style={this.cssStyles.textStyle()}
          >
            {this.props.currentGroupContext.title}
          </StaticContent>
        }
        <Clickable
          onClick={this.handleRequestOpen}
          ref={this.handleMount}
          className="create-group-btn"
          style={{ width: '20px', height: '32px' }}
        >
          {(!this.props.currentGroupContext.title || this.props.isGroupContextDirty) ? (
            <GroupsIcon color={ICON_SELECTED} />
          ) : (
            <GroupsIcon color={ICON_SELECTED} isFilled />
          )}
        </Clickable>
        {groupPopOver}
      </div>
    );
  }
}



