import PropTypes from 'prop-types';
import React from 'react';
import { difference, includes } from 'lodash';
import Switch from '@splunk/react-ui/Switch';
import Dropdown from '@splunk/react-ui/Dropdown';
import Message from '@splunk/react-ui/Message';
import Menu from '@splunk/react-ui/Menu';
import P from '@splunk/react-ui/Paragraph';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import Table from '@splunk/react-ui/Table';
import Link from '@splunk/react-ui/Link';
import Paginator from '@splunk/react-ui/Paginator';
import Select from '@splunk/react-ui/Select';
import Button from '@splunk/react-ui/Button';
import { _ } from '@splunk/ui-utils/i18n';
import { createTestHook } from '../../util/TestSupport';
import TagPill from '../common/TagPill';
import ConfirmationDialog from '../common/ConfirmationDialog';
import { DELETE_OBJECT_STATE, FETCH_OBJECT_STATE, ADMIN_ROLE } from '../../util/StateConstants';
import EMUtils from '../../libs/EMUtils';
import {
  isAllSelected,
  isSelected,
  updateSelectedItems,
} from '../../util/Investigate';

export default class GroupsList extends React.Component {
  /**
   * Generates a link to the Metrics Analysis Workspace
   *
   * @static
   * @param {any} groupRow the group row
   * @returns a string that represents the URL
   *
   * @memberOf GroupsList
   */
  static getGroupUrl(groupRow) {
    // Link to specific group GAW.
    const encodedGroupId = encodeURIComponent(groupRow.name);
    const filters = groupRow.dimensions.reduce((re, curr) => {
      const filterArray = re;
      filterArray[curr[0]] = [...(filterArray[curr[0]] || []), curr[1]];
      return filterArray;
    }, {});
    const encodedDimensions = encodeURIComponent(JSON.stringify(filters));
    return EMUtils.getAppPageUrl(`metrics_analysis?group=${encodedGroupId}&filters=${encodedDimensions}`);
  }

  static propTypes = {
    /**
     * List of groups to be displayed
     */
    filteredGroups: PropTypes.array,
    /**
     * roles for the currently authenticated user
     */
    userRoles: PropTypes.array,
    /**
     * Callback for when a group is selected or deselected
     */
    onGroupSelection: PropTypes.func.isRequired,

    /**
     * Callback when group update.
     * This should update currentTab
     */
    onEditGroup: PropTypes.func.isRequired,

    /**
     * Selected dimensions on entity view tab
     */
    onDimensionSelectionChange: PropTypes.func.isRequired,
    /**
     * Callback for group deletion
     */
    onDeleteGroups: PropTypes.func.isRequired,
    /**
     * String indicating the state of group deletion
     */
    groupDeletionState: PropTypes.oneOf(Object.values(DELETE_OBJECT_STATE)),
    /**
     * Array that contains a list of groups to be deleted
     */
    selectedGroups: PropTypes.array,
    /**
     * Callback for function delete that confirms the deletion
     */
    onGroupDeletionConfirm: PropTypes.func.isRequired,
    /**
     * Callback for function that finishes or abandons confirmation
     */
    onGroupDeletionReady: PropTypes.func.isRequired,
    /**
     * Number of groups to display per page
     */
    groupsPerPage: PropTypes.oneOf([-1, 20, 50, 100]),
    /**
     * The group count offset
     */
    groupsOffset: PropTypes.number,
    /**
     * total group count
     */
    totalGroupCount: PropTypes.number,
    /**
     * Callback for when the pagination occurs, sets selectedGroups to []
     */
    onClearSelectedGroups: PropTypes.func.isRequired,
    /**
     * The order of sorting: asc, desc
     */
    groupSortBy: PropTypes.oneOf(['asc', 'desc']),
    /**
     * The order of sorting: asc, desc
     */
    groupSortKey: PropTypes.oneOf(['title', 'entities_count']),
    /**
     * Callback for when an group is to be sorted
     */
    onGroupSort: PropTypes.func.isRequired,
    /**
     * Callback for when pagination occurs
     */
    onGroupOffsetChange: PropTypes.func.isRequired,
    /**
     * Callback for when group fetch limit changes
     */
    onGroupsPerPageChange: PropTypes.func.isRequired,
    /**
     * Callback for refreshing groups
     */
    onRefreshGroups: PropTypes.func.isRequired,
    /**
     * Callback for loading roles for current authenticated user
     */
    loadRoles: PropTypes.func.isRequired,
    /**
     * Entity last fetched timestamp
     */
    groupsLastFetched: PropTypes.number,
    /**
     * Possible values for number of groups to display per page
     */
    groupsPerPageOptions: PropTypes.arrayOf(PropTypes.number),
    /**
     * State of the entity fetch
     */
    groupFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
  };

  static defaultProps = {
    filteredGroups: [],
    selectedGroups: [],
    userRoles: [],
    groupFetchState: FETCH_OBJECT_STATE.READY,
    groupDeletionState: DELETE_OBJECT_STATE.READY,
    groupsPerPage: 50,
    groupsOffset: 0,
    totalGroupCount: 0,
    groupSortBy: 'asc',
    groupSortKey: 'title',
    groupsLastFetched: -1,
    groupsPerPageOptions: [20, 50, 100],
  };

  constructor(props) {
    super(props);
    this.cssStyles = {
      main: {
        margin: '0px 20px 0px 20px',
      },
      tagPill: {
        marginRight: '5px',
        display: 'inline-block',
      },
      tagPillContainer: {
        display: 'inline-block',
      },
      loadGroupsWaitSpinner: {
        margin: '4px 4px',
      },
      paginatorContainer: {
        height: '40px',
        lineHeight: '40px',
        padding: '5px 0px 5px 5px',
        display: 'flex',
        justifyContent: 'space-between',
      },
      actionsCount: {
        display: 'flex',
      },
      bulkActions: {
        display: 'inline-block',
      },
      groupSelectedCount: {
        marginLeft: '20px',
        marginTop: '11px',
      },
      selectAllLink: {
        marginLeft: '10px',
        marginTop: '1px',
        fontSize: '14px',
      },
    };

    // sortKey needs to match the key in group object
    this.tableSortableColumns = [
      { sortKey: 'title', label: _('Group Name') },
      { sortKey: 'entities_count', label: _('Number of Entities') },
    ];

    this.state = {
      selectAll: false,
    };

    this.handleSelectAllInPage = this.handleSelectAllInPage.bind(this);
    this.handleSingleSelect = this.handleSingleSelect.bind(this);
  }

  componentDidMount() {
    this.props.onRefreshGroups();
    this.props.loadRoles();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.groupDeletionState === DELETE_OBJECT_STATE.SUCCESS) {
      this.props.onGroupDeletionReady();
    }
  }

  getConfirmationDialog() {
    let title = '';
    let message = '';
    let mainMessage = '';
    let infoMessage = '';
    const cannotDelete = !includes(this.props.userRoles, ADMIN_ROLE);
    const confirmDialogContents = this.generateAppropriateConfirmationContents();

    switch (this.props.groupDeletionState) {
      case DELETE_OBJECT_STATE.CONFIRM: {
        title = confirmDialogContents.title;
        mainMessage = confirmDialogContents.confirmationMessage;
        infoMessage = confirmDialogContents.infoMessage;
        message = (
          <div>
            <P>{mainMessage}</P>
            {
              (infoMessage && <Message type="info">{infoMessage}</Message>)
            }
          </div>
        );
        break;
      }
      case DELETE_OBJECT_STATE.ERROR: {
        title = _('Deletion Failed');
        mainMessage = _('Deletion failed!');
        message = <P>{mainMessage}</P>;
        break;
      }
      case DELETE_OBJECT_STATE.IN_PROGRESS: {
        title = _('Deleting...');
        message = <WaitSpinner style={this.cssStyles.loadGroupsWaitSpinner} />;
        break;
      }
      default:
        break;
    }
    return (
      <ConfirmationDialog
        open={
          this.props.groupDeletionState === DELETE_OBJECT_STATE.CONFIRM ||
          this.props.groupDeletionState === DELETE_OBJECT_STATE.IN_PROGRESS ||
          this.props.groupDeletionState === DELETE_OBJECT_STATE.ERROR
        }
        title={title}
        primaryButtonText={_('Delete')}
        className="group-deletion-confirmation-dialog"
        primaryButtonClicked={() => this.handleGroupsDelete()}
        beforeClose={() => this.beforeClose()}
        showCancel
        closeOnClick={false}
        disablePrimaryClick={this.props.groupDeletionState === DELETE_OBJECT_STATE.IN_PROGRESS ||
          cannotDelete}
      >
        {message}
      </ConfirmationDialog>
    );
  }

  getGroupsPerPageDropDown() {
    return (
      <Select
        appearance="pill"
        value={`${this.props.groupsPerPage}`}
        onChange={this.handlePerPageGroupsChange}
        id="groups-per-page"
      >
        {this.props.groupsPerPageOptions.map(option =>
          <Select.Option key={`${option}`} label={`${option} Per Page`} value={`${option}`} />,
        )}
      </Select>
    );
  }

  getSelectAllGroupsLink() {
    const boolAllGroupsInPageSelected = isAllSelected(
      this.props.filteredGroups,
      this.props.selectedGroups,
      'name',
    );
    const totalPages = Math.ceil(
      this.props.totalGroupCount / this.props.groupsPerPage,
    );
    const selectAllText =
      boolAllGroupsInPageSelected &&
      !this.state.selectAll &&
      totalPages !== 1 &&
      _(`Select All ${this.props.totalGroupCount} Groups`);
    const clearAllText =
      boolAllGroupsInPageSelected &&
      this.state.selectAll &&
      totalPages !== 1 &&
      _('Clear Selection');

    return (
      boolAllGroupsInPageSelected && (
        <Link
          id="select-all-items"
          onClick={() => this.handleSelectAll()}
          style={this.cssStyles.selectAllLink}
        >
          {selectAllText}
          {clearAllText}
        </Link>
      )
    );
  }

  generateGroupSelectedCount() {
    let selectionCountText;
    if (this.props.selectedGroups.length === 1) {
      selectionCountText = _(
        `${this.props.selectedGroups.length} Group Selected`,
      );
    } else if (this.state.selectAll) {
      selectionCountText = _(`${this.props.totalGroupCount} Groups Selected`);
    } else {
      selectionCountText = _(
        `${this.props.selectedGroups.length} Groups Selected`,
      );
    }

    return (
      this.props.selectedGroups.length > 0 && (
        <P
          id="groups-selected-count"
          style={this.cssStyles.groupSelectedCount}
        >
          {selectionCountText}
        </P>
      )
    );
  }

  generateAppropriateConfirmationContents() {
    const isDeleteSingleGroup = this.props.selectedGroups.length === 1;
    const isDeleteMultipleGroups =
      this.props.selectedGroups.length > 1 &&
      this.props.selectedGroups.length <= this.props.filteredGroups.length;
    const isDeleteAllGroups = this.state.selectAll;
    const isAdmin = includes(this.props.userRoles, ADMIN_ROLE);
    let title;
    let confirmationMessage;
    let infoMessage;
    if (!isAdmin) {
      const entityVerbage = (isDeleteSingleGroup ? _('the selected group!') : _('the selected groups!'));
      title = _(`Cannot delete ${entityVerbage}`);
      confirmationMessage = _(`Only admin can delete ${entityVerbage}`);
    } else if (isDeleteAllGroups) {
      title = _('Delete All Groups');
      confirmationMessage = _(
        `Are you sure you want to delete all ${this.props
          .totalGroupCount} groups in your environment?`,
      );
      infoMessage = _(
        'Deleting all groups will only remove them from the list. \n' +
        'All associated alerts will also be deleted.',
      );
    } else if (isDeleteMultipleGroups) {
      title = _(`Delete ${this.props.selectedGroups.length} Groups`);
      confirmationMessage = _(
        `Are you sure you want to delete the ${this.props.selectedGroups
          .length} selected groups?`,
      );
      infoMessage = _(
        'Deleting these groups will only remove them from the list. \n' +
        'All associated alerts will also be deleted.',
      );
    } else if (isDeleteSingleGroup) {
      title = _('Delete Group');
      confirmationMessage = _(
        `Are you sure you want to delete the group ${this.props
          .selectedGroups[0].title}?`,
      );
      infoMessage = _(
        'Deleting this group will only remove it from the list. \n' +
        'All associated alerts will also be deleted.',
      );
    }

    return {
      title,
      confirmationMessage,
      infoMessage,
    };
  }

  generateTable() {
    const editLabel = _('Edit');
    const deleteLabel = _('Delete');
    const groupAction = (
      <Button
        label={_('Action')}
        appearance="pill"
        isMenu
      />
    );
    return (
      <Table stripeRows>
        <Table.Head>
          <Table.HeadCell width={60}>
            <Switch
              key="checkall"
              value="checkall"
              onClick={this.handleSelectAllInPage}
              selected={isAllSelected(
                this.props.filteredGroups,
                this.props.selectedGroups,
                'name',
              )}
            />
          </Table.HeadCell>
          {this.tableSortableColumns.map(columnHeaderData => (
            <Table.HeadCell
              key={columnHeaderData.sortKey}
              onSort={this.handleSort}
              sortKey={columnHeaderData.sortKey}
              sortDir={
                columnHeaderData.sortKey === this.props.groupSortKey
                  ? this.props.groupSortBy
                  : 'none'
              }
            >
              {columnHeaderData.label}
            </Table.HeadCell>
          ))}
          <Table.HeadCell>{_('Dimensions')}</Table.HeadCell>
          <Table.HeadCell>{_('Actions')}</Table.HeadCell>
        </Table.Head>
        <Table.Body>
          {this.props.groupFetchState !== FETCH_OBJECT_STATE.IN_PROGRESS &&
            this.props.filteredGroups.map(row => (
              <Table.Row key={row.name}>
                <Table.Cell>
                  <Switch
                    key={row.name}
                    value={row}
                    onClick={this.handleSingleSelect}
                    selected={isSelected(
                      this.props.selectedGroups,
                      row.name,
                      'name',
                    )}
                  />
                </Table.Cell>
                <Table.Cell>
                  {row.numberOfEntities !== 0 ? (
                    <Link to={GroupsList.getGroupUrl(row)}>{row.title}</Link>
                  ) : (
                    <div className="disabled-no-entities">{row.title}</div>
                  )}
                </Table.Cell>
                <Table.Cell>{row.numberOfEntities}</Table.Cell>
                <Table.Cell>
                  <div style={this.cssStyles.tagPillContainer}>
                    {row.dimensions.map(dimension => (
                      <TagPill
                        key={JSON.stringify(dimension)}
                        data={{ type: dimension[0], name: dimension[1] }}
                        value={{ type: dimension[0], name: dimension[1] }}
                        style={this.cssStyles.tagPill}
                      />
                    ))}
                  </div>
                </Table.Cell>
                <Table.Cell style={{ paddingLeft: '0px' }}>
                  <Dropdown toggle={groupAction} style={{ minWidth: '60px' }}>
                    <Menu>
                      <Menu.Item onClick={() => this.handleEditGroup(row)}>
                        {editLabel}
                      </Menu.Item>
                      <Menu.Item onClick={() => this.handleConfirmDelete(row)}>
                        {deleteLabel}
                      </Menu.Item>
                    </Menu>
                  </Dropdown>
                </Table.Cell>
              </Table.Row>
            ))}
        </Table.Body>
      </Table>
    );
  }

  handlePerPageGroupsChange = (e, { value }) => {
    this.props.onGroupsPerPageChange(parseInt(value, 10));
  };

  /**
   * Handle select all groups
   */
  handleSelectAll() {
    if (this.state.selectAll) {
      this.props.onClearSelectedGroups();
    }
    this.setState({ selectAll: !this.state.selectAll });
  }

  /**
   * Handle select all groups in page
   */
  handleSelectAllInPage() {
    const visibleGroups = this.props.filteredGroups;
    const groupsToBeToggled = difference(
      visibleGroups,
      this.props.selectedGroups,
    );
    if (groupsToBeToggled.length > 0) {
      this.props.onGroupSelection(
        updateSelectedItems(this.props.selectedGroups, groupsToBeToggled),
      );
    } else {
      this.props.onGroupSelection(
        updateSelectedItems(this.props.selectedGroups, visibleGroups),
      );
    }
  }

  /**
   * Handle selecting a single group
   */
  handleSingleSelect = (e, { value }) => {
    this.props.onGroupSelection(
      updateSelectedItems(this.props.selectedGroups, [value]),
    );
  };

  /**
   * Handles sorting of the group listing table
   *
   * @memberOf GroupList
   */
  handleSort = (e, { sortKey }) => {
    this.props.onGroupSort(sortKey);
  };

  /**
   * Handle group edit action
   * will switch back to entity view with dimensionSelection change
   */
  handleEditGroup = (row) => {
    const currentGroupContext = {
      name: row.name,
      title: row.title,
      filter: row.dimensions,
    };
    this.props.onEditGroup(row.dimensions, currentGroupContext);
    this.props.onDimensionSelectionChange(row.dimensions);
  };

  /**
   * Handles page selection for the listing table
   * TODO: replace with REST call
   *
   * @memberOf GroupList
   */
  handlePageChange = (e, { page }) => {
    this.props.onGroupOffsetChange((page - 1) * this.props.groupsPerPage);
  };

  beforeClose() {
    if (this.state.selectAll) {
      this.setState({ selectAll: false });
    }
    this.props.onGroupDeletionReady();
  }

  handleConfirmDelete(row) {
    this.props.onGroupDeletionConfirm(row);
  }

  handleConfirmBulkDelete() {
    this.props.onGroupDeletionConfirm(this.props.selectedGroups);
  }

  handleGroupsDelete() {
    this.props.onDeleteGroups({
      groupNames: this.props.selectedGroups.map(group => group.name),
      deleteAll: this.state.selectAll,
    });
  }

  render() {
    const totalPages = Math.ceil(
      this.props.totalGroupCount / this.props.groupsPerPage,
    );
    const currentPage = (this.props.groupsOffset / this.props.groupsPerPage) + 1;
    const deleteSelectedGroups = _('Delete Selected Groups');
    const bulkActions = (
      <Button
        label={_('Bulk Actions')}
        id="bulk-actions-btn"
        disabled={this.props.selectedGroups.length === 0}
        appearance="pill"
        isMenu
      />
    );
    const table = this.generateTable();
    const numberOfGroupsPage = this.getGroupsPerPageDropDown();
    const confirmationDialog = this.getConfirmationDialog();

    return (
      <div
        style={this.cssStyles.main}
        data-updated={this.props.groupsLastFetched}
        {...createTestHook(__filename, name)}
      >
        {confirmationDialog}
        <div style={this.cssStyles.paginatorContainer}>
          <div style={this.cssStyles.actionsCount}>
            <div style={this.cssStyles.bulkActions}>
              <Dropdown toggle={bulkActions} id="bulk-actions">
                <Menu>
                  <Menu.Item onClick={() => this.handleConfirmBulkDelete()}>
                    {deleteSelectedGroups}
                  </Menu.Item>
                </Menu>
              </Dropdown>
            </div>
            {this.generateGroupSelectedCount()}
            {this.getSelectAllGroupsLink()}
          </div>
          <div>
            {numberOfGroupsPage}
            <Paginator
              onChange={this.handlePageChange}
              current={currentPage}
              totalPages={totalPages}
              alwaysShowLastPageLink
            />
          </div>
        </div>
        {table}
      </div>
    );
  }
}



