import React from 'react';
import PropTypes from 'prop-types';
import { _ } from '@splunk/ui-utils/i18n';
import Button from '@splunk/react-ui/Button';
import RefreshIcon from '@splunk/react-icons/RotateCounter';

import { INTERVAL_CHOICES } from '../../util/StateConstants';
import RefreshControlCustomSelect from './RefreshControlCustomSelect';

export default class RefreshControl extends React.Component {
  static propTypes = {
    refreshInterval: PropTypes.number,
    onRefresh: PropTypes.func.isRequired,
    onUpdateRefreshInterval: PropTypes.func.isRequired,
    mainStyles: PropTypes.object,
  }

  static defaultProps = {
    refreshInterval: -1,
    onRefresh: null,
    onUpdateRefreshInterval: null,
    mainStyles: {},
  }

  constructor(props) {
    super(props);

    this.cssStyles = {
      timeRange: {
        display: 'inline-block',
      },
      main: {
        fontSize: '12px',
        display: 'inline-flex',
        alignItems: 'center',
        ...this.props.mainStyles,
      },
      manualRefresh: {
        padding: '0 4px 0 8px',
      },
      menuTitle: {
        fontSize: '12px',
        padding: '0.4rem 1.6rem',
      },
      dropDownWrapper: {
        minWidth: '1.2rem',
        maxWidth: '100%',
        padding: '0 4px 0 0',
      },
      refreshTextStyle: {
        fontSize: 10,
        fontWeight: 100,
        color: '#999999',
        paddingRight: '10px',
      },
    };

    this.state = { timer: this.props.refreshInterval };

    this.updateTimer = this.updateTimer.bind(this);
  }

  componentDidMount() {
    if (this.props.refreshInterval !== -1) {
      this.intervalId = setInterval(this.updateTimer, 1000);
    }
  }

  componentWillUnmount() {
    if (this.intervalId) {
      clearInterval(this.intervalId);
      delete this.intervalId;
    }
  }

  onChange = (refreshTime) => {
    this.refreshTimerComponent(refreshTime);
  }

  refreshTimerComponent(refreshTime) {
    // First, clear any previous interval that was running
    clearInterval(this.intervalId);

    // Trigger the action to update prop
    this.props.onUpdateRefreshInterval(refreshTime);

    // Reset timer to initial state
    this.setState({
      timer: refreshTime,
    });

    // Start timer again
    if (refreshTime !== -1) {
      this.intervalId = setInterval(this.updateTimer, 1000);
    }
  }

  updateTimer() {
    this.setState({
      timer: this.state.timer <= 0 ? this.props.refreshInterval : this.state.timer - 1,
    });

    if (this.state.timer === 0) {
      // refresh
      this.props.onRefresh();
    }
  }

  render() {
    const manualRefreshButton = this.props.refreshInterval === -1 ? (
      <Button
        id="refresh-btn"
        icon={<RefreshIcon screenReaderText={null} width="12px" height="12px" />}
        label={_('Refresh')}
        onClick={() => { this.props.onRefresh(); }}
        appearance="pill"
        style={this.cssStyles.manualRefresh}
        data-test="refresh-control"
      />
    ) : (
      undefined
    );

    const autoRefreshLabel = this.props.refreshInterval !== -1 ? 'Auto-refresh' : undefined;

    const message = this.props.refreshInterval !== -1 ? (
      <div style={this.cssStyles.refreshTextStyle}>
          Auto refresh in {this.state.timer}s
      </div>
    ) : (
      undefined
    );

    const selectionOptions = Array.from(INTERVAL_CHOICES.keys()).map(seconds => (
      <RefreshControlCustomSelect.Option
        label={INTERVAL_CHOICES.get(seconds)}
        value={seconds}
        key={seconds}
      />
    ));

    return (
      <div style={this.cssStyles.main}>
        {manualRefreshButton}
        <div style={this.cssStyles.dropDownWrapper}>
          <RefreshControlCustomSelect
            menuLabel={autoRefreshLabel}
            refreshInterval={this.props.refreshInterval}
            header={<div style={this.cssStyles.menuTitle}>
              {_('AUTO REFRESH')}
            </div>}
            onChange={this.onChange}
          >
            {selectionOptions}
          </RefreshControlCustomSelect>
        </div>
        {message}
      </div>

    );
  }
}



