import { TILE_LIST_TOGGLE } from '../actionTypes';

/**
 *
 * Action to set the current view to either tile or list
 * @param {string} selectedView - the view selected by the user
 * @returns - an action object
 */
export default function tileListToggle(selectedView) {
  return {
    type: TILE_LIST_TOGGLE,
    selectedView,
  };
}



