import React from 'react';
import PropTypes from 'prop-types';
import Radium from 'radium';
import { isEqual, sortBy } from 'lodash';

import CardLayout from '@splunk/react-ui/CardLayout';

import { FETCH_OBJECT_STATE, TILE_RESOLUTION } from '../../../util/StateConstants';
import { AVAILABILITY } from '../ColorByOption';
import TileLayout from './TileLayout';
import EntityTileMedium from './EntityTileMedium';
import EntityTileSmall from './EntityTileSmall';
import {
  entityHealthToSortValue,
  TILE_STYLES_MEDIUM,
  TILE_STYLES_SMALL,
} from './EntityTileConstants';

const EntityTileView = class EntityTileView extends React.Component {
  static propTypes = {
    /**
     * List of entities to be displayed
     */
    filteredEntities: PropTypes.array,

    /**
     * The resolution that the tiles need to be rendered in
     */
    entityTileResolution: PropTypes.oneOf(Object.values(TILE_RESOLUTION)),

    /**
     * Callback when the tile resolution has been changed
     */
    onEntityTileResolutionChange: PropTypes.func.isRequired,

    /**
     * The threshold metric name
     */
    entityThresholdMetric: PropTypes.string,

    /**
     * Dictionary of threshold data by entity name
     */
    entityThresholdData: PropTypes.object,
    /**
     * Entity last fetched timestamp
     */
    entitiesLastFetched: PropTypes.number,

    /**
     * State of the metric data fetch
     */
    metricDataFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),

    /**
     * Callback for refreshing entities
     */
    onRefreshEntities: PropTypes.func.isRequired,
  };

  static defaultProps = {
    filteredEntities: [],
    entityThresholdMetric: AVAILABILITY,
    metricDataFetchState: FETCH_OBJECT_STATE.READY,
    entityTileResolution: TILE_RESOLUTION.MEDIUM,
    entityThresholdData: {},
    entitiesLastFetched: -1,
  };

  constructor(props) {
    super(props);

    this.styles = () => {
      switch (this.props.entityTileResolution) {
        case TILE_RESOLUTION.MEDIUM:
          return { ...TILE_STYLES_MEDIUM };
        case TILE_RESOLUTION.SMALL:
          return { ...TILE_STYLES_SMALL };
        default:
          return {};
      }
    };
  }

  componentDidMount() {
    this.props.onRefreshEntities();
  }

  /**
   * Runs before the component is rendered
   *
   * @param {any} nextProps - the props about to be processed
   * @param {any} nextState - the state about to be processed
   *
   * @memberOf EntityTileView
   */
  shouldComponentUpdate(nextProps) {
    return !(this.props.metricDataFetchState !== nextProps.metricDataFetchState
              && isEqual(this.props.entityThresholdData, nextProps.entityThresholdData));
  }

  render() {
    // Unlike the lister, we need to do some sorting based on the
    // an evaluated threshold. Since thresholds can be dynamically selected,
    // this cannot be done server-side without bad user experience
    const sortedEntities = sortBy(
      sortBy(this.props.filteredEntities, 'title'),
      value => entityHealthToSortValue[value.entityHealth],
    );
    const entityTiles = sortedEntities.map((entity) => {
      switch (this.props.entityTileResolution) {
        case TILE_RESOLUTION.SMALL:
          return (
            <EntityTileSmall
              key={entity.name}
              entity={entity}
            />
          );
        case TILE_RESOLUTION.MEDIUM:
          return (
            <EntityTileMedium
              key={entity.name}
              entity={entity}
              entityThresholdMetric={this.props.entityThresholdMetric}
            />
          );
        default:
          return '';
      }
    });

    return (
      <TileLayout
        onTileResolutionChange={this.props.onEntityTileResolutionChange}
        tileResolution={this.props.entityTileResolution}
        supportedResolutions={[TILE_RESOLUTION.SMALL, TILE_RESOLUTION.MEDIUM]}
        dataLastFetched={this.props.entitiesLastFetched}
        className="entity-tile"
      >
        <CardLayout alignCards="left" {...this.styles().layout}>
          {entityTiles}
        </CardLayout>
      </TileLayout>
    );
  }
};

export default Radium(EntityTileView);



