import { takeLatest, put, call, select } from 'redux-saga/effects';
import lodash from 'lodash';
import { _ } from '@splunk/ui-utils/i18n';
import * as groupAPI from '../services/groups';
import * as entityAPI from '../services/entities';
import * as groupRetrieveAction from '../actions/groups/GroupRetrieve';
import * as groupCreationAction from '../actions/groups/GroupCreation';
import * as groupFilterRetrieve from '../actions/groups/GroupFilterRetrieve';
import * as groupUpdateAction from '../actions/groups/GroupUpdate';
import * as groupDeletionAction from '../actions/groups/GroupDeletion';
import { computeGroupsHealth } from '../actions/groups/GroupHealthCompute';
import { clearSelectedGroups } from '../actions/groups/GroupSelection';
import { getSelectedView, getCurrentTab } from './controlBar';
import { TILE_VIEW, GROUP_VIEW_TAB } from '../util/StateConstants';
import * as actionTypes from '../actions/actionTypes';
import EMUtils from '../libs/EMUtils';

export const getGroupFilters = state => state.groupsReducer.groupFilters;

export const fetchParameters = state => ({
  count: state.groupsReducer.groupsFetchLimit,
  offset: state.groupsReducer.groupsOffset,
  sort_key: state.groupsReducer.groupSortKey,
  sort_dir: state.groupsReducer.groupSortBy,
});

function buildGroupStatus(entries) {
  const res = {
    totalCount: entries.length,
  };
  const countObj = lodash.countBy(entries, entry => entry.content.state);
  res.activeCount = countObj.active || 0;
  res.inactiveCount = countObj.inactive || 0;
  const inactivePercentage = Math.floor((res.inactiveCount / res.totalCount) * 100);
  if (inactivePercentage >= 20) {
    res.status = 'inactive';
  } else {
    res.status = 'active';
  }
  if (inactivePercentage > 0) {
    res.message = _(`${inactivePercentage}% of the servers are inactive`);
  } else {
    res.message = _('100% of the servers are active');
  }

  return res;
}

/**
 * Formats the backed group filter string into a 2D array
 *
 * @param {any} filterString - the backend filter string
 * @returns - a 2D array of dimensions
 */
function formatGroupFilter(filterString) {
  if (filterString) {
    const dimensionStringArray = filterString.split(',').map(s => s.trim());
    const dimensionObjectArray = dimensionStringArray.map(item => item.split('='));
    return dimensionObjectArray;
  }
  return [];
}

/**
 * Formats the backend group response for easy digestion by components
 *
 * @param {any} response - the group EAI response
 * @returns - a formatted EAI response
 */
function formatGroupResponse(response) {
  if (response && response.entry) {
    const formattedEntries = response.entry.map((entry) => {
      const updatedEntry = lodash.cloneDeep(entry);
      updatedEntry.content.filter = formatGroupFilter(entry.content.filter);
      updatedEntry.content.logSearch = JSON.parse(entry.content.log_search || '{}');
      return updatedEntry;
    });
    response.entry = formattedEntries;
  }
  return response;
}

/**
 * Create group saga . Makes a request with the group name and filter specified.
 * @param  {[object]} action action object with data.
 * @return {Generator}
 */
export function* createGroup(action) {
  const filters = [];
  yield put(groupCreationAction.addNewGroup.start());

  if (!action.filter || !action.title) {
    yield put(groupCreationAction.addNewGroup.failure({ message: _('missing filters or title') }));
    return;
  }

  // filter stored as an object :
  // ex: a1,b=2
  action.filter.forEach((el) => {
    filters.push(`${el[0]}=${el[1]}`);
  });

  const data = {
    title: action.title,
    name: action.name,
    filter: filters.join(','),
  };
  try {
    const { response } = yield call(groupAPI.addNewGroup, data);
    const formattedResponse = formatGroupResponse(response);
    yield put(groupCreationAction.addNewGroup.success(formattedResponse));
    // update filter, this should be done last, since this is async
    yield put(groupFilterRetrieve.loadGroupFilter());
  } catch (error) {
    yield put(groupCreationAction.addNewGroup.failure(error));
  }
}

/**
* Load group info saga, this generator takes care of
* fetching a single group's info
* dispatches status action START/SUCCESS/ERROR
*
* @memberOf metricsGroupSaga
* @param action with attribute groupId
*/
export function* loadGroupInfo(action) {
  const groupId = action.id;

  yield put(groupRetrieveAction.groupRetrieveTracker.start());
  if (!groupId) {
    yield put(groupRetrieveAction.groupRetrieveTracker.failure({ message: _('missing groupId') }));
    return;
  }

  // try {
  let groupResponse = yield call(groupAPI.getGroupByName, groupId);
  groupResponse = groupResponse.response;
  if (groupResponse && groupResponse.entry && groupResponse.entry.length > 0) {
    const groupFilter = groupResponse.entry[0].content.filter;
    const formattedResponse = formatGroupResponse(groupResponse);
    const groupResult = formattedResponse.entry[0].content;
    const groupFilterPairs = EMUtils.formatGroupFilter(groupFilter);
    const queryString = EMUtils.pairsToObjectString(groupFilterPairs, 'dimensions.');
    let entityResponse = yield call(entityAPI.getEntities, {}, queryString);
    entityResponse = entityResponse.response;

    if (entityResponse && entityResponse.entry && entityResponse.entry.length >= 0) {
      groupResult.groupStatus = buildGroupStatus(entityResponse.entry);
      yield put(groupRetrieveAction.groupRetrieveTracker.success(groupResult));
    } else {
      throw _('Invalid response returning entities associated with this group!');
    }
  } else {
    throw _('Invalid response returning group information!');
  }
  // } catch (error) {
  // yield put(groupRetrieveAction.groupRetrieveTracker.failure(error));
  // }
}

/**
* Load Groups saga, this generator take care of fetch groups job,
* dispatch status action START/SUCCESS/ERROR
*
* @memberOf groupSaga
*/
export function* loadGroups(clearSelections = false, action) {
  let totalGroupsKVStore = -1;
  const groupFilters = yield select(getGroupFilters);
  if (clearSelections) {
    yield put(clearSelectedGroups());
  }
  const currentTab = yield select(getCurrentTab);
  if (currentTab !== GROUP_VIEW_TAB) {
    return;
  }
  yield put(groupRetrieveAction.groupRetrieveTracker.start());
  try {
    let newFetchParameters;
    const existingFetchParameters = yield select(fetchParameters);
    const selectedView = yield select(getSelectedView);
    if (selectedView === TILE_VIEW) {
      newFetchParameters = {
        ...existingFetchParameters,
        count: -1,
        offset: 0,
        ...action.data,
      };
    } else {
      newFetchParameters = {
        ...existingFetchParameters,
        ...action.data,
      };
    }

    const groupCountResponse = yield call(groupAPI.getTotalNumOfGroups);

    const countSuccess = groupCountResponse.response;
    const countError = groupCountResponse.error;

    if (countSuccess && countSuccess.entry) {
      totalGroupsKVStore = countSuccess.paging.total;
    } else {
      yield put(groupRetrieveAction.groupRetrieveTracker.failure(countError));
    }

    const groupFilterQuery = groupFilters.length === 0 ? {} : { title: groupFilters };
    const { response } = yield call(groupAPI.getGroups, newFetchParameters, groupFilterQuery);

    const updated = new Date(response.updated || Date.now());
    const formattedResponse = formatGroupResponse(response);
    const totalCount = response.paging.total;
    yield put(groupRetrieveAction.groupRetrieveTracker.success({
      formattedResponse,
      totalGroupsKVStore,
      totalCount,
      updated: updated.getTime(),
    }));
    yield put(computeGroupsHealth());
  } catch (error) {
    yield put(groupRetrieveAction.groupRetrieveTracker.failure(error));
  }
}

/**
 * Load groups metadata.
 * @return {Generator} fetches all group names to be shown in the
 * groups drop down.
 */
export function* loadGroupFilter() {
  yield put(groupFilterRetrieve.groupFilterRetrieveTracker.start());
  const { response, error } = yield call(groupAPI.getGroupsMetadata);
  if (response) {
    const payload = response.entry.length ? response.entry[0].content :
      { titles: [] };
    yield put(groupFilterRetrieve.groupFilterRetrieveTracker.success(payload));
  } else {
    yield put(groupFilterRetrieve.groupFilterRetrieveTracker.failure(error));
  }
}

/**
* Update Group saga, this generator take care of fetch groups job,
* dispatch status action START/SUCCESS/ERROR
*
* @memberOf groupSaga
*/
export function* updateGroup(action) {
  const filters = [];
  yield put(groupUpdateAction.updateExistingGroup.start());

  if (!action.name) {
    yield put(groupUpdateAction.updateExistingGroup.failure({ message: _('missing target name of group') }));
    return;
  }
  if (!action.filter || !action.title) {
    yield put(groupUpdateAction.updateExistingGroup.failure({ message: _('missing filters or title') }));
    return;
  }

  action.filter.forEach((el) => {
    filters.push(`${el[0]}=${el[1]}`);
  });

  const data = {
    title: action.title,
    filter: filters.join(','),
  };

  try {
    const { response } = yield call(groupAPI.updateGroup, action.name, data);
    const formattedResponse = formatGroupResponse(response);
    yield put(groupUpdateAction.updateExistingGroup.success(formattedResponse));
    // update filter, this should be done last, since this is async
    yield put(groupFilterRetrieve.loadGroupFilter());
  } catch (error) {
    yield put(groupUpdateAction.updateExistingGroup.failure(error));
  }
}

/**
* Delete group saga, it makes a DELETE request with group names
* @param {[object]}    action object with data.
* @return {Generator}
*/
export function* deleteGroups(action) {
  let totalGroupsKVStore = -1;
  const { groupNames, deleteAll } = action;
  yield put(groupDeletionAction.deleteGroupTracker.start());
  try {
    const existingFetchParameters = yield select(fetchParameters);
    const groupTitleFilters = yield select(getGroupFilters);
    const groupTitleFiltersQuery = groupTitleFilters.length > 0 ? { title: groupTitleFilters } : {};

    const { response } = yield call(
      groupAPI.deleteGroups,
      groupNames,
      JSON.stringify(groupTitleFiltersQuery),
      deleteAll,
      existingFetchParameters);
    const formattedResponse = formatGroupResponse(response);
    const totalCount = response.paging.total;
    const updated = new Date(response.updated || Date.now());

    const groupCountResponse = yield call(groupAPI.getTotalNumOfGroups);

    const countSuccess = groupCountResponse.response;
    const countError = groupCountResponse.error;

    if (countSuccess && countSuccess.entry) {
      totalGroupsKVStore = countSuccess.paging.total;
    } else {
      yield put(groupDeletionAction.deleteGroupTracker.failure(countError));
    }

    yield put(groupDeletionAction.deleteGroupTracker.success({
      formattedResponse,
      totalGroupsKVStore,
      totalCount,
      updated: updated.getTime(),
    }));
    // update filter, this should be done last, since this is async
    yield put(groupFilterRetrieve.loadGroupFilter());
  } catch (error) {
    yield put(groupDeletionAction.deleteGroupTracker.failure(error));
  }
}


// Main saga generator, it could handle group of multiple sagas
export default function* groupSaga() {
  yield takeLatest(actionTypes.GROUP_CREATE, createGroup);
  yield takeLatest(actionTypes.GROUP_RETRIEVE, loadGroups, false);
  yield takeLatest(actionTypes.GROUP_REFRESH, loadGroups, true);
  yield takeLatest(actionTypes.GROUP_REFRESH, loadGroupFilter);
  yield takeLatest(actionTypes.GROUP_UPDATE, updateGroup);
  yield takeLatest(actionTypes.GROUP_DELETE, deleteGroups);
  yield takeLatest(actionTypes.GROUP_FILTER_RETRIEVE, loadGroupFilter);

  // Filtering, sorting and pagination sagas
  yield takeLatest(actionTypes.GROUP_OFFSET_CHANGE, loadGroups, true);
  yield takeLatest(actionTypes.UPDATE_GROUP_FILTERS, loadGroups, true);
  yield takeLatest(actionTypes.GROUPS_LISTING_SORT_GROUPS, loadGroups, true);
  yield takeLatest(actionTypes.GROUPS_PER_PAGE_CHANGE, loadGroups, true);
}

export function* metricsGroupSaga() {
  yield takeLatest(actionTypes.GROUP_RETRIEVE, loadGroupInfo);
}



