import React, { Component } from 'react';
import PropTypes from 'prop-types';

import P from '@splunk/react-ui/Paragraph';
import SplunkwebConnector from '@splunk/react-time-range/SplunkwebConnector';
import TimeRangeDropdown from '@splunk/react-time-range/Dropdown';
import { _ } from '@splunk/ui-utils/i18n';

import WaitSpinner from '@splunk/react-ui/WaitSpinner';

import WildcardMultiSelect from '../common/WildcardMultiSelect';
import ToggleButtonGroup from './ToggleButtonGroup';
import ToggleTileListControl from './ToggleTileListControl';
import {
  FETCH_OBJECT_STATE,
  GROUP_VIEW_TAB,
  LIST_VIEW,
  TILE_VIEW,
} from '../../util/StateConstants';
import { percentFormatter } from '../../util/Investigate';
import Divider from '../common/Divider';
import ControlBar from '../common/ControlBar';
import { ICON_SELECTED } from '../common/Colors';
import ThresholdControl from '../common/ThresholdControl';
import RefreshControl from '../common/RefreshControl';


export default class GroupControlBar extends Component {
    static propTypes = {
      /**
       * Equivalent to WildcardMultiSelect props
       */
      filteredGroupTitles: PropTypes.arrayOf(PropTypes.string).isRequired,
      onGroupFilterChange: PropTypes.func.isRequired,
      onGroupSelectionChange: PropTypes.func.isRequired,
      groupMultiSelectPlaceholder: PropTypes.string,

      /**
       * The list of groups filters that have already been selected
       */
      groupFilters: PropTypes.arrayOf(PropTypes.string),

      /**
       * Flag to indicate whether the timerange picker should be shown
       */
      showTimeRangePicker: PropTypes.bool,

      /**
       * Callback when a new time range has been picked
       */
      onTimeRangeChange: PropTypes.func.isRequired,

      /**
       * The earliest time to examine
       */
      earliestTime: PropTypes.string,

      /**
       * Equivalent to ToggleButtonGroup props
       */
      currentTab: PropTypes.string,
      onToggle: PropTypes.func.isRequired,

      /**
       * Selected view for groups: either tile or list
       */
      selectedView: PropTypes.oneOf([LIST_VIEW, TILE_VIEW]),
      /**
       * Equivalent to ToggleTileListControl prop
       */
      onListTileToggle: PropTypes.func.isRequired,
      /**
       * Group refresh dispatch action
       * @type {[function]}
       */
      onRefreshGroups: PropTypes.func.isRequired,
      /**
       * total group count
       */
      totalGroupCount: PropTypes.number,
      /**
       * State of the group fetch
       */
      groupFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
      /**
       * Callback for when a new threshold is selected
       */
      onUpdateGroupThresholdInfo: PropTypes.func.isRequired,
      groupThresholdValueMinimum: PropTypes.number.isRequired,
      groupThresholdValueMaximum: PropTypes.number.isRequired,
      groupThresholdValue: PropTypes.number.isRequired,
      groupThresholdEvaluator: PropTypes.func.isRequired,
      onComputeGroupsHealth: PropTypes.func.isRequired,
      /**
       * Auto-refresh interval
       */
      refreshInterval: PropTypes.number,
      /**
       * Callback to update refresh interval
       */
      onUpdateRefreshInterval: PropTypes.func.isRequired,
    }

    static defaultProps = {
      totalGroupCount: 0,
      groupFetchState: FETCH_OBJECT_STATE.READY,
      selectedDimensions: [],
      groupFilters: [],
      groupMultiSelectPlaceholder: _('Start typing to filter groups by name'),
      earliestTime: '-7d@d',
      showTimeRangePicker: false,
      currentTab: GROUP_VIEW_TAB,
      selectedView: LIST_VIEW,
      autoRefreshEnabled: false,
      refreshInterval: -1,
    }

    constructor(props, context) {
      super(props, context);
      this.cssStyles = {
        timeRange: {
          display: 'inline-block',
        },
        multiselectGroup: {
          display: 'inherit',
          lineHeight: '40px',
          verticalAlign: 'middle',
          flexGrow: '100',
          marginLeft: '10px',
          marginRight: '20px',
        },
        multiselectStyle: {
          flexGrow: 'inherit',
        },
        viewTypeSelector: {
          float: 'right',
          marginRight: '20px',
        },
        groupIconStyle: {
          backgroundColor: ICON_SELECTED,
          textAlign: 'center',
          width: '12px',
          height: '12px',
          padding: '2px',
          paddingBottom: '4px',
          margin: '0px 2px',
        },
        navContainer: {
          height: '100%',
          width: '100%',
          lineHeight: '40px',
          paddingLeft: '20px',
          display: 'flex',
        },
        groupCountStyle: {
          marginRight: '14px',
          marginTop: '11px',
        },
        divider: {
          display: 'inline-block',
        },
        waitSpinnerStyle: {
          position: 'relative',
          marginLeft: '10px',
          top: '15px',
        },
        refreshTextStyle: {
          fontSize: 24,
          fontWeight: 100,
        },
      };
    }

    onThresholdUpdate = (threshold) => {
      this.props.onUpdateGroupThresholdInfo(threshold);
      this.props.onComputeGroupsHealth();
    }

    handleToggle = (selectedTab) => {
      if (selectedTab !== this.props.currentTab) {
        this.props.onToggle(selectedTab);
      }
    }

    handleUpdateRefreshInterval = (interval) => {
      this.props.onUpdateRefreshInterval(interval);
    }

    render() {
      const multiselect =
            (
              <WildcardMultiSelect
                multiselectStyle={this.cssStyles.multiselectStyle}
                options={this.props.filteredGroupTitles}
                selectedValues={this.props.groupFilters}
                onFilterChange={this.props.onGroupFilterChange}
                onValueChange={this.props.onGroupSelectionChange}
                placeholder={this.props.groupMultiSelectPlaceholder}
              />
            );

      const groupCountString = this.props.totalGroupCount === 1 ? _(`${this.props.totalGroupCount} Group`) :
        _(`${this.props.totalGroupCount} Groups`);

      return (
        <div>
          <ControlBar id="nav-top" borderBottom={false}>
            <ToggleButtonGroup
              currentTab={this.props.currentTab}
              onToggle={this.handleToggle}
            />
            {
              this.props.showTimeRangePicker &&
              <div style={this.cssStyles.timeRange}>
                <SplunkwebConnector>
                  <TimeRangeDropdown
                    onChange={this.props.onTimeRangeChange}
                    earliest={this.props.earliestTime}
                    latest="now"
                    appearance="pill"
                    formInputTypes={[]}
                    advancedInputTypes={[]}
                  />
                </SplunkwebConnector>
              </div>
            }
            <div style={this.cssStyles.multiselectGroup}>
              {multiselect}
            </div>
          </ControlBar>
          <ControlBar id="nav-bottom" borderBottom>
            <div style={this.cssStyles.navContainer}>
              <div style={this.cssStyles.groupCountStyle}>
                <P id="group-count">{groupCountString}</P>
              </div>
              <div style={this.cssStyles.divider}>
                <Divider />
              </div>
              {this.props.selectedView === TILE_VIEW &&
              <div>
                <ThresholdControl
                  thresholdEvaluator={this.props.groupThresholdEvaluator}
                  thresholdValueMinimum={this.props.groupThresholdValueMinimum}
                  thresholdValueMaximum={this.props.groupThresholdValueMaximum}
                  thresholdValue={this.props.groupThresholdValue}
                  onUpdateThresholdInfo={this.onThresholdUpdate}
                  labelFormatter={percentFormatter}
                />
                <div style={this.cssStyles.divider}>
                  <Divider />
                </div>
              </div>
              }
              <div style={this.cssStyles.refreshTextStyle}>
                <RefreshControl
                  refreshInterval={this.props.refreshInterval}
                  onRefresh={this.props.onRefreshGroups}
                  onUpdateRefreshInterval={this.handleUpdateRefreshInterval}
                />
              </div>
              {this.props.groupFetchState === FETCH_OBJECT_STATE.IN_PROGRESS &&
              <WaitSpinner style={this.cssStyles.waitSpinnerStyle} />
              }
            </div>
            <ToggleTileListControl
              selectedView={this.props.selectedView}
              onListTileToggle={this.props.onListTileToggle}
            />
          </ControlBar>
        </div>
      );
    }
}



