import {
  ENTITY_REFRESH,
} from '../actionTypes';

/**
 * Action to refresh entity table and filter entities based on selectedDimensions.
 *
 * @export
 * @returns - an action object
 */
export function refreshEntities() {
  return {
    type: ENTITY_REFRESH,
  };
}

export default refreshEntities;



