import { SET_URL_PARAMS } from '../../actions/actionTypes';

const defaultState = {
  urlParams: '',
};

const urlReducer = (state = defaultState, action) => {
  switch (action.type) {
    case SET_URL_PARAMS:
      return {
        ...state,
        urlParams: action.urlParams,
      };

    default:
      return state;
  }
};

export default urlReducer;



