import {
  GROUP_UPDATE,
  START,
  SUCCESS,
  FAILURE,
} from '../actionTypes';
import ActionUtils from '../actionUtils';

// This part will be used for saga
export const GROUP_UPDATE_TRACKER = ActionUtils.createRequestTypes(GROUP_UPDATE);

export const updateExistingGroup = {
  start: () => ActionUtils.createAction(GROUP_UPDATE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(GROUP_UPDATE_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(GROUP_UPDATE_TRACKER[FAILURE], { error }),
};

export const updateGroup = groupInfo => ActionUtils.createAction(GROUP_UPDATE, groupInfo);



