import {
  GROUP_RETRIEVE,
  START,
  SUCCESS,
  FAILURE,
} from '../actionTypes';
import ActionUtils from '../actionUtils';

export const GROUP_RETRIEVE_TRACKER = ActionUtils.createRequestTypes(GROUP_RETRIEVE);

/**
 * Return action for each status
 */
export const groupRetrieveTracker = {
  start: () => ActionUtils.createAction(GROUP_RETRIEVE_TRACKER[START], {}),
  success: response => ActionUtils.createAction(GROUP_RETRIEVE_TRACKER[SUCCESS], { response }),
  failure: error => ActionUtils.createAction(GROUP_RETRIEVE_TRACKER[FAILURE], { error }),
};

export const loadGroups = data => ActionUtils.createAction(GROUP_RETRIEVE, data);
export const loadGroup = groupIdObj => ActionUtils.createAction(GROUP_RETRIEVE, groupIdObj);



