import React from 'react';
import pick from 'lodash/pick';
import PropTypes from 'prop-types';


import { createTestHook } from '../../util/TestSupport';
import EntitiesListingContents from './EntitiesListing';
import GroupListingContents from './GroupsListing';
import EntityControlBar from './EntityControlBar';
import GroupControlBar from './GroupControlBar';
import EntityTileView from './tileviews/EntityTileView';
import GroupTileView from './tileviews/GroupTileView';
import { ENTITY_VIEW_TAB, LIST_VIEW }
  from '../../util/StateConstants';
import EntitiesEmptyStatePage from './EntitiesEmpty';
import GroupsEmptyStatePage from './GroupsEmpty';

export default class InvestigatePageContents extends React.Component {
  static propTypes = {
    ...EntityControlBar.propTypes,
    ...GroupControlBar.propTypes,
    ...EntitiesListingContents.propTypes,
    ...EntityTileView.propTypes,
    ...GroupTileView.propTypes,
    ...GroupListingContents.propTypes,
    ...GroupsEmptyStatePage.propTypes,
    totalEntitiesKVStore: PropTypes.number,
    totalGroupsKVStore: PropTypes.number,

  }

  static defaultProps = {
    ...EntityControlBar.defaultProps,
    ...GroupControlBar.defaultProps,
    ...EntitiesListingContents.defaultProps,
    ...EntityTileView.defaultProps,
    ...GroupTileView.defaultProps,
    ...GroupListingContents.defaultProps,
    totalEntitiesKVStore: -1,
    totalGroupsKVStore: -1,
  }

  render() {
    const entityListingProps = pick(this.props, Object.keys(EntitiesListingContents.propTypes));
    const entityTileViewProps = pick(this.props, Object.keys(EntityTileView.propTypes));
    const groupListingProps = pick(this.props, Object.keys(GroupListingContents.propTypes));
    const groupTileViewProps = pick(this.props, Object.keys(GroupTileView.propTypes));
    const entityControlBarProps = pick(this.props, Object.keys(EntityControlBar.propTypes));
    const groupControlBarProps = pick(this.props, Object.keys(GroupControlBar.propTypes));
    const groupEmptyStatePageProps = pick(this.props, Object.keys(GroupsEmptyStatePage.propTypes));
    return (
      <div {...createTestHook(__filename, name)}>
        {
          this.props.currentTab === ENTITY_VIEW_TAB ?
            (
              <div>
                <EntityControlBar {...entityControlBarProps} />
                {
                  this.props.totalEntitiesKVStore === 0 ? <EntitiesEmptyStatePage /> :
                    (
                      <div>
                        {
                          this.props.selectedView === LIST_VIEW ?
                            (
                              <EntitiesListingContents {...entityListingProps} />
                            ) :
                            (
                              <EntityTileView {...entityTileViewProps} />
                            )
                        }
                      </div>
                    )
                }
              </div>
            ) :
            (
              <div>
                <GroupControlBar {...groupControlBarProps} />
                {
                  this.props.totalGroupsKVStore === 0 ?
                    <GroupsEmptyStatePage
                      {...groupEmptyStatePageProps}
                    /> :
                    (
                      <div>
                        {
                          this.props.selectedView === LIST_VIEW ?
                            (
                              <GroupListingContents {...groupListingProps} />
                            ) :
                            (
                              <GroupTileView {...groupTileViewProps} />
                            )
                        }
                      </div>
                    )
                }
              </div>
            )
        }
      </div>
    );
  }
}



