import React from 'react';
import PropTypes from 'prop-types';
import RadioBar from '@splunk/react-ui/RadioBar';
import { _ } from '@splunk/ui-utils/i18n';
import Star from '@splunk/react-icons/Star';

import EntitiesIcon from '../common/Icons/Entities';

import { createTestHook } from '../../util/TestSupport';
import { ENTITY_VIEW_TAB, GROUP_VIEW_TAB } from '../../util/StateConstants';

export default class ToggleButtonGroup extends React.Component {
  static propTypes = {
    /**
     * The current tab
     */
    currentTab: PropTypes.string.isRequired,

    /**
     * Callback when toggle.
     * This should update currentTab
     */
    onToggle: PropTypes.func.isRequired,
  }

  constructor(props, context) {
    super(props, context);
    this.cssStyles = {
      viewSelector: {
        marginLeft: '20px',
        display: 'flex',
      },

      groupIconContainer: {
        display: 'flex',
        backgroundColor: 'grey',
        width: '10px',
        height: '10px',
        paddingLeft: '2px',
        paddingTop: '2px',
      },

      entityIconContainer: {
        display: 'flex',
        width: '12px',
        height: '10px',
      },
    };
  }

  handleToggle = (e, { value }) => {
    this.props.onToggle(value);
  };

  render() {
    return (
      <div {...createTestHook(__filename, name)} >
        <div style={this.cssStyles.viewSelector}>
          <RadioBar onChange={this.handleToggle} value={this.props.currentTab}>
            <RadioBar.Option
              icon={
                <div style={this.cssStyles.entityIconContainer}>
                  <EntitiesIcon width="12px" height="12px" color="grey" />
                </div>
              }
              value={ENTITY_VIEW_TAB}
              label={_('Entities')}
            />
            <RadioBar.Option
              icon={
                <div style={this.cssStyles.groupIconContainer}>
                  <Star width="8px" height="8px" preserveAspectRatio="xMaxYMid" style={{ color: 'white' }} />
                </div>
              }
              value={GROUP_VIEW_TAB}
              label={_('Groups')}
            />
          </RadioBar>
        </div>
      </div>
    );
  }
}



