import {
  SET_URL_PARAMS,
  SET_STATE_FROM_URL,
  FETCH_WITH_OPTIONS,
} from '../actionTypes';

/** updateUrlParams
 * @param string -- string of url params
 * @returns SET_URL_PARAMS action object
 */
export function updateUrlParams(urlParams) {
  return {
    type: SET_URL_PARAMS,
    urlParams,
  };
}

/** updateStateFromUrlParams
 * @param object -- object of url parameters
 * @returns SET_STATE_FROM_URL action object
 */
export function updateStateFromUrlParams(parsedParams) {
  return {
    type: SET_STATE_FROM_URL,
    data: parsedParams,
  };
}

/**
 */
export const fetchWithOptions = (fetchOptions = null, viewOptions) => ({
  type: FETCH_WITH_OPTIONS,
  fetchOptions,
  viewOptions,
});



