import {
  HEALTHY_FLAT,
  UNKNOWN_FLAT,
  UNHEALTHY_FLAT,
  HEALTHY_HOVER,
  UNHEALTHY_HOVER,
} from '../../common/Colors';

export const ENTITY_HEALTH = {
  HEALTHY: 'HEALTHY',
  UNHEALTHY: 'UNHEALTHY',
  UNKNOWN: 'UNKNOWN',
};

export const entityHealthToSortValue = {
  [ENTITY_HEALTH.UNHEALTHY]: 0,
  [ENTITY_HEALTH.HEALTHY]: 1,
  [ENTITY_HEALTH.UNKNOWN]: 2,
};

// TODO: convert these to .css (need to get a loader first)
export const TILE_STYLES_MEDIUM = {
  cardBody: {
    display: 'flex',
    padding: '8px',
    margin: '2.5px',
    cursor: 'pointer',
    fontWeight: 'inherit',
    position: 'relative',
    minWidth: '104px',
    maxWidth: '104px',
    minHeight: '104px',
    maxHeight: '104px',
  },
  title: {
    overflow: 'hidden',
    wordWrap: 'break-word',
    wordBreak: 'break-all',
    color: 'white',
    fontSize: '14px',
    lineHeight: '14px',
    fontWeight: 'inherit',
    margin: '2px',
  },
  layout: {
    cardWidth: 120,
    cardMaxWidth: 120,
    cardMinWidth: 120,
  },
  lastUpdated: {
    position: 'absolute',
    top: '45%',
    left: '0%',
    minWidth: '120px',
    textAlign: 'center',
    color: 'white',
    fontSize: '24px',
  },
  tooltip: {
    maxHeight: '125px',
  },
  clock: {
    fontSize: '12px',
    marginRight: '3px',
    flex: 'none',
  },
  errorIcon: {
    marginTop: '2px',
    marginRight: '8px',
    color: 'white',
    width: '10px',
    height: '10px',
  },
  assetInfo: {
    position: 'absolute',
    fontSize: '10px',
    lineHeight: '14px',
    color: 'white',
    bottom: '10px',
    left: '10px',
    opacity: '0.78',
  },
  entityHealthAttributes: {
    [ENTITY_HEALTH.HEALTHY]: {
      backgroundColor: HEALTHY_FLAT,
      ':hover': {
        backgroundColor: HEALTHY_HOVER,
      },
    },
    [ENTITY_HEALTH.UNHEALTHY]: {
      backgroundColor: UNHEALTHY_FLAT,
      fontWeight: 'bold',
      ':hover': { backgroundColor: UNHEALTHY_HOVER },
    },
    [ENTITY_HEALTH.UNKNOWN]: { backgroundColor: UNKNOWN_FLAT },
  },
};

export const TILE_STYLES_SMALL = {
  cardBody: {
    display: 'flex',
    margin: '1.5px',
    cursor: 'pointer',
    fontWeight: 'inherit',
    position: 'relative',
    minWidth: '45px',
    maxWidth: '45px',
    minHeight: '45px',
    maxHeight: '45px',
  },
  layout: {
    cardWidth: 45,
    cardMaxWidth: 45,
    cardMinWidth: 45,
  },
  tooltip: {
    maxHeight: '48px',
  },
  errorIcon: {
    marginTop: '15px',
    marginLeft: '15px',
    color: 'white',
    width: '15px',
    height: '15px',
  },
  entityHealthAttributes: {
    [ENTITY_HEALTH.HEALTHY]: {
      backgroundColor: HEALTHY_FLAT,
      ':hover': {
        backgroundColor: HEALTHY_HOVER,
      },
    },
    [ENTITY_HEALTH.UNHEALTHY]: {
      backgroundColor: UNHEALTHY_FLAT,
      fontWeight: 'bold',
      ':hover': { backgroundColor: UNHEALTHY_HOVER },
    },
    [ENTITY_HEALTH.UNKNOWN]: { backgroundColor: UNKNOWN_FLAT },
  },
};



