import React from 'react';
import PropTypes from 'prop-types';
import Dropdown from '@splunk/react-ui/Dropdown';
import Button from '@splunk/react-ui/Button';
import Menu from '@splunk/react-ui/Menu';

const Option = (props) => {
  const { label, ...menuProps } = props;
  return (
    <Menu.Item
      selectable
      style={{
        fontSize: '12px',
      }}
      {...menuProps}
      data-test={label}
    >
      {label}
    </Menu.Item>
  );
};

export default class RefreshControlCustomSelect extends React.Component {
  static propTypes = {
    refreshInterval: PropTypes.number,
    menuLabel: PropTypes.string,
    header: PropTypes.node,
    children: PropTypes.array,
    onChange: PropTypes.func.isRequired,
  }

  static defaultProps = {
    refreshInterval: -1,
    header: null,
    onChange: null,
    children: [],
    menuLabel: '',
  }

  handleChange = (value) => {
    if (value !== this.props.refreshInterval) {
      this.props.onChange(value);
    }
  };

  render() {
    const dropDownStyle = {
      margin: '0 !important',
      padding: 0,
      borderRadius: '0 3px 3px 0',
    };
    const paddedDownStyle = {
      margin: '0 !important',
      padding: '0.4rem',
    };
    const toggle = (
      <Button
        id="refresh-time-select"
        style={this.props.menuLabel ? paddedDownStyle : dropDownStyle}
        label={this.props.menuLabel}
        appearance="pill"
        isMenu
      />
    );
    const decoratedOptions = React.Children.map(this.props.children, child =>
      React.cloneElement(child, {
        onClick: () => this.handleChange(child.props.value),
        selected: this.props.refreshInterval === child.props.value,
        autoFocus: this.props.refreshInterval === child.props.value,
      }),
    );

    return (
      <Dropdown
        style={{
          maxWidth: '100%',
          margin: '0 !important',
          padding: 0,
          borderRadius: '0 3px 3px 0',
        }}
        toggle={toggle}
      >
        {this.props.header}
        <Menu id="refresh-time-option">
          {decoratedOptions}
        </Menu>
      </Dropdown>
    );
  }
}

Option.propTypes = Menu.Item.propTypes;

RefreshControlCustomSelect.Option = Option;



