import React, { Component } from 'react';
import Button from '@splunk/react-ui/Button';
import Heading from '@splunk/react-ui/Heading';
import { _ } from '@splunk/ui-utils/i18n';
import Tiles from '@splunk/react-icons/Tiles';
import { getAppPageUrl } from '../../libs/EMUtils';
import { createTestHook } from '../../util/TestSupport';

export default class EntitiesEmptyStatePage extends Component {
  constructor(props, context) {
    super(props, context);
    this.cssStyles = {
      layout: {
        textAlign: 'center',
        marginTop: '50px',
      },
      tileLeft: {
        marginTop: '5px',
        marginRight: '5px',
        color: 'grey',
      },
      tileRight: {
        marginTop: '5px',
        color: 'grey',
      },
      heading1: {
        textAlign: 'center',
      },
      heading3: {
        marginTop: '15px',
        marginBottom: '30px',
        textAlign: 'center',
      },
      paragraph: {
        textAligh: 'center',
      },
    };
  }

  render() {
    const configurePageURL = getAppPageUrl('configure');
    const heading1 = _("You haven't added any entities.");
    const heading3 = _('Go to the configuration page to set up data collection to discover new entities.');
    return (
      <div
        style={this.cssStyles.layout}
        {...createTestHook(__filename, name)}
      >
        <div>
          <Tiles
            style={this.cssStyles.tileLeft}
            width="24px"
            height="24px"
            preserveAspectRatio="xMaxYMid"
          />
          <Tiles
            style={this.cssStyles.tileRight}
            width="24px"
            height="24px"
            preserveAspectRatio="xMaxYMid"
          />
        </div>
        <div>
          <Tiles
            style={this.cssStyles.tileLeft}
            width="24px"
            height="24px"
            preserveAspectRatio="xMaxYMid"
          />
          <Tiles
            style={this.cssStyles.tileRight}
            width="24px"
            height="24px"
            preserveAspectRatio="xMaxYMid"
          />
        </div>
        <Heading
          level={1}
          style={this.cssStyles.heading1}
        >
          {heading1}
        </Heading>
        <Heading
          level={3}
          style={this.cssStyles.heading3}
        >
          {heading3}
        </Heading>
        <Button data-test="add-entities-button" label="Add Entities" appearance="primary" to={configurePageURL} />
      </div>
    );
  }
}



