import {
  GROUP_SELECT,
  GROUP_SELECT_CLEAR,
} from '../actionTypes';

/**
 * Action to select a single group from the group listing table.
 *
 * @export
 * @param {any} groups - The group is selected
 * @returns - an action object
 */
export const groupSelect = groups => ({
  type: GROUP_SELECT,
  groups,
});

export const clearSelectedGroups = () => ({
  type: GROUP_SELECT_CLEAR,
});



